import { t } from 'i18next';
import { useMutation, useQuery } from 'react-query';
import {
	noticeDetailAPI,
	noticeEditAPI,
	noticeListAPI,
	noticeRegisterAPI,
	noticeUndoAPI,
	preSignedUrlAPI,
} from '../apis/noticeApi';
import { NoticeRegisterRequest, PreSignedUrlRequest } from '../types/request';
import { BaseResponse, NoticeDetailResponse, NoticeListResponse, PreSignedUrlResponse } from '../types/response';
import { checkTodayInRange, conversionDate, getRegisteredDate, sortAscending } from '../utils/moment';

const useNoticeListQuery = () => {
	const query = useQuery<NoticeListResponse, number>(['noticeList'], () => noticeListAPI(), {
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
		select: (data: NoticeListResponse) => {
			const selectData = [...data.list.sort((a, b) => sortAscending(a.create_time, b.create_time))].map(
				(item, index) => ({
					...item,
					index: data.list.length - index,
					exposure: checkTodayInRange(item.start_time, item.end_time),
					registered: getRegisteredDate(conversionDate(item.create_time, 'YYYY-MM-DD')),
				})
			);

			return {
				...data,
				list: [...selectData.filter((item) => item.exposure), ...selectData.filter((item) => !item.exposure)],
			};
		},
	});

	return query;
};

const useNoticeDetailQuery = (id: string) => {
	const query = useQuery<NoticeDetailResponse, number>(['noticeDetail', id], () => noticeDetailAPI(id), {
		enabled: !!id,
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
		select: (data: NoticeDetailResponse) => {
			return {
				...data,
				data: {
					...data.data,
					exposure: checkTodayInRange(data.data.start_time, data.data.end_time),
					registered: getRegisteredDate(conversionDate(data.data.create_time, 'YYYY-MM-DD')),
				},
			};
		},
	});

	const item = query.data?.data;

	return { ...query, item };
};

const useNoticeUndoMutation = (onSuccess: () => void) => {
	const query = useMutation<BaseResponse, number, { id: string }>(
		(variables: { id: string }) => noticeUndoAPI(variables.id),
		{
			onSuccess,
			onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
		}
	);

	return query;
};

const usePreSignedUrlQuery = (
	preSignedUrlPayload: PreSignedUrlRequest,
	onSuccess: (response: PreSignedUrlResponse) => void,
	onError: (error: number) => void
) => {
	const query = useQuery(['preSignedUrl'], () => preSignedUrlAPI(preSignedUrlPayload), {
		enabled: false,
		onSuccess,
		onError,
	});

	return query;
};

const useNoticeRegisterMutation = (onSuccess: () => void, onError: (error: number) => void) => {
	const query = useMutation<BaseResponse, number, NoticeRegisterRequest>(
		(body: NoticeRegisterRequest) => noticeRegisterAPI(body),
		{
			onSuccess,
			onError,
		}
	);

	return query;
};

const useNoticeEditMutation = (id: number, onSuccess: () => void, onError: (error: number) => void) => {
	const query = useMutation<BaseResponse, number, { id: string; body: NoticeRegisterRequest }>(
		['noticeEdit', id],
		({ id, body }) => noticeEditAPI(id, body),

		{
			onSuccess,
			onError,
		}
	);

	return query;
};

export {
	useNoticeUndoMutation,
	useNoticeDetailQuery,
	useNoticeEditMutation,
	useNoticeListQuery,
	useNoticeRegisterMutation,
	usePreSignedUrlQuery,
};
