import { t } from 'i18next';
import { memo } from 'react';
import { UserListType } from '../../../../types/auth';
import { conversionDate } from '../../../../utils/moment';
import { cn } from '../../../../utils/style';
import { ListItemStyle } from './style';

interface ItemProps {
	item: UserListType;
	activeUser: number;
	onClick: (id: number) => void;
}

const ListItem = ({ item, activeUser, onClick }: ItemProps) => {
	return (
		<li className={cn(ListItemStyle({ active: item.user_id === activeUser }))} onClick={() => onClick(item.user_id)}>
			<div className='grow'>
				<p className='mb-2 text-base text-gray-900 break-all text-ellipsis-1'>{item.learner_name}</p>
				<p className='mb-1 text-sm text-gray-500 break-all text-ellipsis-1'>{item.login_id}</p>
			</div>
			<div className='self-end flex-none mt-4 ml-auto'>
				<div className='text-sm font-medium text-slate-500'>
					{t('[날짜]에 생성됨', { date: conversionDate(item.create_time, 'YYYY-MM-DD HH:mm:ss') })}
				</div>
			</div>
		</li>
	);
};

export default memo(
	ListItem,
	(prev: ItemProps, next: ItemProps) =>
		prev.item === next.item && prev.activeUser === next.activeUser && prev.onClick === next.onClick
);
