import { PencilIcon } from '@heroicons/react/24/solid';
import { t } from 'i18next';
import { useNavigate } from 'react-router-dom';
import Button from '../../../components/Button/Button';
import Radio from '../../../components/Radio/Radio';
import Search from '../../../components/Search/Search';
import { MESSAGE_SEND_TO_INDIVIDUAL_URL } from '../../../constants/router';

interface SearchBarProps {
	keyword: string;
	type: string;
	onSelect: (value: string) => void;
	onChange: (value: string) => void;
	onReset: () => void;
	onSearch: () => void;
}

const SearchBar = ({ keyword, type, onSelect, onChange, onReset, onSearch }: SearchBarProps) => {
	const navigate = useNavigate();

	return (
		<div className='px-10 pb-6 border-b-[1px] border-gray-200 flex gap-4 items-center'>
			<div className='flex gap-4'>
				<Radio id='learnerName' selected={type} label={t('학생 이름')} onChange={onSelect} />
				<Radio id='loginId' selected={type} label={t('유저 아이디')} onChange={onSelect} />
			</div>
			<Search keyword={keyword} onChange={onChange} onSearch={onSearch} onReset={onReset} placeholder='키워드 입력' />
			<Button
				color='black'
				size='md'
				label={t('작성')}
				leftIcon={<PencilIcon />}
				className='ml-auto'
				onClick={() => {
					navigate(MESSAGE_SEND_TO_INDIVIDUAL_URL);
				}}
			/>
		</div>
	);
};

export default SearchBar;
