import { useMemo, useState } from 'react';
import Layout from '../../components/Layout/Layout';
import NoData from '../../components/NoData/NoData';
import { useNoticeListQuery } from '../../queries/noticeQuery';
import NoticeDetail from './components/NoticeDetail';
import NoticeList from './components/NoticeList';
import SearchBar from './components/SearchBar';

const Notice = () => {
	const [keyword, setKeyword] = useState('');
	const [activeNotice, setActiveNotice] = useState<{ id: string; index: number }>({ id: '', index: 0 });

	const noticeList = useNoticeListQuery();

	const filterList = useMemo(() => {
		const list = noticeList.data?.list.filter((item) => item.title.includes(keyword) || item.body.includes(keyword));

		if (!list?.filter((item) => item.notice_id === activeNotice.id).length) setActiveNotice({ id: '', index: 0 });

		return list;
	}, [noticeList.dataUpdatedAt]);

	const onSearch = () => {
		noticeList.refetch();
	};

	const onReset = () => {
		setKeyword('');
		setActiveNotice({ id: '', index: 0 });
		noticeList.refetch();
	};

	return (
		<Layout>
			<SearchBar keyword={keyword} onChange={(value) => setKeyword(value)} onReset={onReset} onSearch={onSearch} />
			<div className='flex grow w-full h-[calc(100vh_-_188px)]'>
				{!filterList?.length ? (
					<NoData image='non_founded_data' label='검색 결과가 존재하지 않습니다.' />
				) : (
					<>
						<NoticeList
							noticeList={noticeList.data?.list}
							filterList={filterList}
							activeNotice={activeNotice}
							setActiveNotice={setActiveNotice}
						/>
						<NoticeDetail activeNotice={activeNotice} />
					</>
				)}
			</div>
		</Layout>
	);
};

export default Notice;
