import { t } from 'i18next';
import { useMutation, useQuery } from 'react-query';
import {
	messageAllDetailAPI,
	messageAllListAPI,
	messageIndividualDetailAPI,
	messageIndividualListAPI,
	messageRegisterAllPostAPI,
	messageRegisterIndividualPostAPI,
} from '../apis/messageApi';
import { IndividualMessageRegisterRequest, MessageRegisterRequest } from '../types/request';
import {
	BaseResponse,
	MessageAllDetailResponse,
	MessageAllListResponse,
	MessageIndividualDetailResponse,
	MessageIndividualListResponse,
} from '../types/response';
import { conversionDate, getRegisteredDate, sortAscending } from '../utils/moment';

const useMessageAllListQuery = () => {
	const query = useQuery<MessageAllListResponse, number>(['messageAllList'], () => messageAllListAPI(), {
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
		select: (data: MessageAllListResponse) => {
			const selectData = [...data.list.sort((a, b) => sortAscending(a.create_time, b.create_time))].map(
				(item, index) => ({
					...item,
					index: data.list.length - index,
					registered: getRegisteredDate(conversionDate(item.create_time, 'YYYY-MM-DD')),
				})
			);

			return {
				...data,
				list: selectData,
			};
		},
	});

	return query;
};

const useMessageAllDetailQuery = (id: number) => {
	const query = useQuery<MessageAllDetailResponse, number>(['messageAllDetail', id], () => messageAllDetailAPI(id), {
		enabled: !!id,
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
		select: (data: MessageAllDetailResponse) => {
			return {
				...data,
				data: {
					...data.data,
					registered: getRegisteredDate(conversionDate(data.data.create_time, 'YYYY-MM-DD')),
				},
			};
		},
	});

	const item = query.data?.data;

	return { ...query, item };
};

const useMessageIndividualListQuery = (id: number, onSuccess: () => void) => {
	const query = useQuery<MessageIndividualListResponse, number>(
		['messageList', id],
		() => messageIndividualListAPI(id),
		{
			enabled: !!id,
			onSuccess,
			onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
			select: (data: MessageIndividualListResponse) => {
				const selectData = [...data.list.sort((a, b) => sortAscending(a.create_time, b.create_time))].map(
					(item, index) => ({
						...item,
						index: data.list.length - index,
						registered: getRegisteredDate(conversionDate(item.create_time, 'YYYY-MM-DD')),
					})
				);

				return {
					...data,
					list: selectData,
				};
			},
		}
	);

	return query;
};

const useMessageIndividualDetailQuery = (id: number) => {
	const query = useQuery<MessageIndividualDetailResponse, number>(
		['messageDetail', id],
		() => messageIndividualDetailAPI(id),
		{
			enabled: id !== -1,
			onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
			select: (data: MessageIndividualDetailResponse) => {
				return {
					...data,
					data: {
						...data.data,
						registered: getRegisteredDate(conversionDate(data.data.create_time, 'YYYY-MM-DD')),
					},
				};
			},
		}
	);

	const item = query.data?.data;

	return { ...query, item };
};

const useMessageAllRegisterMutation = (onSuccess: () => void, onError: (error: number) => void) => {
	const query = useMutation<BaseResponse, number, MessageRegisterRequest>(
		(body: MessageRegisterRequest) => messageRegisterAllPostAPI(body),
		{
			onSuccess,
			onError,
		}
	);

	return query;
};

const useMessageIndividualRegisterMutation = (onSuccess: () => void, onError: (error: number) => void) => {
	const query = useMutation<BaseResponse, number, IndividualMessageRegisterRequest>(
		(body: IndividualMessageRegisterRequest) => messageRegisterIndividualPostAPI(body),
		{
			onSuccess,
			onError,
		}
	);

	return query;
};

export {
	useMessageAllDetailQuery,
	useMessageAllListQuery,
	useMessageAllRegisterMutation,
	useMessageIndividualDetailQuery,
	useMessageIndividualListQuery,
	useMessageIndividualRegisterMutation,
};
