import { t } from 'i18next';
import { useEffect, useState } from 'react';
import { Trans } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import Button from '../../components/Button/Button';
import Input from '../../components/Input/Input';
import Layout from '../../components/Layout/Layout';
import { IMAGE_SRC } from '../../constants/image';
import { NOTICE_URL } from '../../constants/router';
import useInput from '../../hooks/useInput';
import { useLoginQuery } from '../../queries/authQuery';
import useAuth from '../../stores/useAuth';
import useToast from '../../stores/useToast';
import { InputValueType } from '../../types/common';

const Login = () => {
	const navigate = useNavigate();
	const [inputValues, setInputValues] = useState<InputValueType>({
		username: { value: '', error: '' },
		password: { value: '', error: '' },
	});
	const { resetToken } = useAuth();
	const { openToast, closeToast } = useToast();
	const { handleInputsValue, handleInputsError } = useInput(setInputValues);

	const login = useLoginQuery(
		inputValues.username.value,
		inputValues.password.value,
		() => {
			navigate(NOTICE_URL);
			closeToast();
		},
		(error: number) => {
			if (error === -2901) handleInputsError('username', t('올바른 아이디를 입력해주세요.'));
			if (error === -2902) handleInputsError('password', t('올바른 비밀번호를 입력해주세요.'));
			if (error === -2903) {
				handleInputsError('username', t('올바른 아이디를 입력해주세요.'));
				handleInputsError('password', t('올바른 비밀번호를 입력해주세요.'));
			}
			if (error !== -2901 && error !== -2902 && error !== -2903) alert(t('알 수 없는 문제가 발생하였습니다.'));
		}
	);

	const handleCopyText = () => {
		navigator.clipboard.writeText('devops@hodoolabs.com');
		closeToast();
	};

	const handleOpenToast = () => {
		openToast({
			title: t('아이디나 비밀번호를 잊으셨나요?'),
			description: <>{t('아이디나 비밀번호를 잊으셨다면, devops@hodoolabs.com으로 연락해주세요.')}</>,
			rightButton: { text: t('연락 정보 복사'), onClick: handleCopyText },
			isClose: true,
			leftIcon: 'toast_question',
			position: 'center',
		});
	};

	const handleLogin = () => {
		if (inputValues.username.value && inputValues.password.value) {
			login.refetch();
		} else {
			if (!inputValues.username.value) {
				handleInputsError('username', t('아이디를 입력해주세요.'));
			}

			if (!inputValues.password.value) {
				handleInputsError('password', t('비밀번호를 입력해주세요.'));
			}
		}
	};

	useEffect(() => {
		resetToken();
	}, []);

	return (
		<Layout sideBar={false} title={false} lang={true} footer={true}>
			<main className='w-[384px] flex items-center flex-col position-center'>
				<img className='w-[122px] h-[81px] mb-6' src={IMAGE_SRC.betia_login_logo} alt='betia' />
				<p className='mb-3 text-5xl font-bold leading-snug text-gray-900'>{t('로그인')}</p>
				<p className='mb-8 text-center text-gray-500 leading-[32.5px] text-xl font-medium'>
					<Trans i18nKey='어서오세요 베티아 잉글리시 시스템 관리 툴입니다!' />
				</p>
				<form className='flex flex-col w-full gap-4'>
					<Input
						size='lg'
						label={<>{t('아이디')}</>}
						value={inputValues.username.value}
						error={inputValues.username.error}
						placeholder={t('아이디를 입력하세요.')}
						onChange={(value) => handleInputsValue('username', value)}
						onError={(error) => handleInputsError('username', error)}
						onEnter={handleLogin}
					/>
					<Input
						type='password'
						size='lg'
						label={<>{t('비밀번호')}</>}
						value={inputValues.password.value}
						error={inputValues.password.error}
						placeholder={t('비밀번호를 입력하세요.')}
						onChange={(value) => handleInputsValue('password', value)}
						onError={(error) => handleInputsError('password', error)}
						onEnter={handleLogin}
					/>
					<Button color='black' size='lg' label={t('로그인')} isFull={true} onClick={() => handleLogin()} />
					<p
						className='text-sm font-medium leading-tight text-center text-gray-700 underline cursor-pointer'
						onClick={handleOpenToast}
					>
						{t('아이디나 비밀번호를 잊으셨나요?')}
					</p>
				</form>
			</main>
		</Layout>
	);
};

export default Login;
