import { t } from 'i18next';
import useModal from '../../../stores/useModal';
import Button from '../../Button/Button';

interface UndoPostingProps {
	onConfirm: () => void;
}

const DiscardNotice = ({ onConfirm }: UndoPostingProps) => {
	const { closeModal } = useModal();

	return (
		<div className='flex gap-3'>
			<Button color='gray' size='lg' isFull={true} label={t('취소')} onClick={closeModal} />
			<Button color='black' size='lg' isFull={true} label={t('작성 취소')} onClick={onConfirm} />
		</div>
	);
};

export default DiscardNotice;
