import axios from 'axios';
import { NoticeRegisterRequest, PreSignedUrlRequest } from '../types/request';
import { BaseResponse, NoticeDetailResponse, NoticeListResponse, PreSignedUrlResponse } from '../types/response';
import axiosInstance from './axiosInstance';

const noticeListAPI = async () => {
	const response = await axiosInstance.get<NoticeListResponse>('/api/v1/notice/list');

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const noticeDetailAPI = async (id: string) => {
	const response = await axiosInstance.get<NoticeDetailResponse>(`/api/v1/notice/${id}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const noticeUndoAPI = async (id: string) => {
	const response = await axiosInstance.delete<BaseResponse>(`/api/v1/notice/${id}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const preSignedUrlAPI = async (preSignedUrlPayload: PreSignedUrlRequest) => {
	const response = await axiosInstance.post<PreSignedUrlResponse>('/api/v1/files/pre-signed-post', preSignedUrlPayload);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const uploadS3ImageAPI = async (url: string, body: FormData) => {
	const response = await axios.post<{ status: number }>(url, body, {
		headers: { 'Content-Type': 'multipart/form-data' },
	});

	if (response.status === 204) return true;
};

const noticeRegisterAPI = async (body: NoticeRegisterRequest) => {
	const response = await axiosInstance.post<BaseResponse>('/api/v1/notice', body);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const noticeEditAPI = async (id: string, body: NoticeRegisterRequest) => {
	const response = await axiosInstance.put<BaseResponse>(`/api/v1/notice/${id}`, body);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

export {
	noticeDetailAPI,
	noticeEditAPI,
	noticeListAPI,
	noticeRegisterAPI,
	noticeUndoAPI,
	preSignedUrlAPI,
	uploadS3ImageAPI,
};
