import Title from '../../sendMessageToAll/components/Title';
import { t } from 'i18next';
import Input from '../../../components/Input/Input';
import Button from '../../../components/Button/Button';
import { MinusIcon, PlusIcon } from '@heroicons/react/24/outline';

interface NumberOfRewardsProps {
	numberOfRewards: string;
	setNumberOfRewards: (value: string) => void;
}

const getFormattedNumber = (number: number) => {
	const toStringFormattedNumber = String(number.toLocaleString('ko-KR'));
	if (toStringFormattedNumber === '0') return '';
	return toStringFormattedNumber;
};

const NumberOfRewards = ({ numberOfRewards, setNumberOfRewards }: NumberOfRewardsProps) => {
	const handleNumberOfRewardsValue = (inputValue: string) => {
		const removedCommaValue = inputValue.replaceAll(',', '');
		// eslint-disable-next-line @typescript-eslint/no-explicit-any
		if (isNaN(Number(removedCommaValue as any))) return;
		if (inputValue === '0') setNumberOfRewards('');
		if (Number(removedCommaValue) < 1 && removedCommaValue.length > 0) return setNumberOfRewards(inputValue);
		return setNumberOfRewards(getFormattedNumber(Number(removedCommaValue)));
	};

	return (
		<div className='flex flex-col gap-8 mb-8'>
			<div>
				<Title className='mb-2'>{t('루미의 수')}</Title>
				<div className='flex gap-3'>
					<Input type='text' onChange={handleNumberOfRewardsValue} size='lg' value={numberOfRewards}></Input>
					<div className='border border-gray-200 rounded-xl'>
						<Button
							leftIcon={<MinusIcon />}
							size='lg'
							onClick={() => setNumberOfRewards(String(Number(numberOfRewards) - 1))}
							disabled={Number(numberOfRewards) <= 1}
							color='white'
						></Button>
					</div>
					<div className='border border-gray-200 rounded-xl'>
						<Button
							leftIcon={<PlusIcon />}
							size='lg'
							onClick={() => setNumberOfRewards(String(Number(numberOfRewards) + 1))}
							color='white'
						></Button>
					</div>
				</div>
			</div>
		</div>
	);
};

export default NumberOfRewards;
