import { uploadS3ImageAPI } from '../apis/noticeApi';
import { PreSignedUrlType } from '../types/notice';

const handleUpload = (preSignedRes: PreSignedUrlType, file: File) => {
	const formData = new FormData();
	formData.append('key', preSignedRes.fields.key);
	formData.append('acl', preSignedRes.fields.acl);
	formData.append('x-amz-security-token', preSignedRes.fields['x-amz-security-token']);
	formData.append('x-amz-credential', preSignedRes.fields['x-amz-credential']);
	formData.append('x-amz-algorithm', preSignedRes.fields['x-amz-algorithm']);
	formData.append('x-amz-date', preSignedRes.fields['x-amz-date']);
	formData.append('policy', preSignedRes.fields.policy);
	formData.append('x-amz-signature', preSignedRes.fields['x-amz-signature']);
	formData.append('file', file);

	if (preSignedRes) {
		if (requestUploadS3Image(`${preSignedRes.url}`, formData)) return true;
	}
};

const requestUploadS3Image = async (url: string, body: FormData) => {
	const response = await uploadS3ImageAPI(url, body);
	return response;
};

export { handleUpload };
