import { t } from 'i18next';
import { memo, useEffect, useRef } from 'react';
import { Trans } from 'react-i18next';
import NoData from '../../../components/NoData/NoData';
import { useUserDetailQuery } from '../../../queries/authQuery';
import { conversionDate } from '../../../utils/moment';
import DetailItem from './DetailItem/DetailItem';

interface LearnerDetailProps {
	activeUser: number;
}

const LearnerDetail = ({ activeUser }: LearnerDetailProps) => {
	const ref = useRef<HTMLDivElement>(null);

	const userDetail = useUserDetailQuery(activeUser);
	const item = userDetail.item;

	useEffect(() => {
		ref.current?.scrollTo({ top: 0 });
	}, [activeUser]);

	return (
		<div className='w-[1025px] overflow-auto scroll-none' ref={ref}>
			{activeUser ? (
				<div className='px-10 py-10'>
					<p className='mb-12 text-2xl font-bold text-gray-700'>{t('상세 정보')}</p>
					<div className='mb-12'>
						<p className='mb-2 text-base font-bold text-gray-700'>{t('학습자 프로필')}</p>
						<div className='flex gap-10'>
							<div className='flex flex-col w-full'>
								<DetailItem title={t('사용자 ID')} content={item?.users.login_id || '-'} />
								<DetailItem title={t('친구 코드')} content={item?.user_profiles.friend_code || '-'} />
								<DetailItem title={t('학습자 이름')} content={item?.users.learner_name || '-'} />
								<DetailItem
									title={t('캐릭터 생성')}
									content={
										!!item?.user_profiles.create_time &&
										conversionDate(item?.user_profiles.create_time, 'YYYY-MM-DD HH:mm')
									}
								/>
							</div>
							<div className='flex flex-col w-full'>
								<DetailItem title={t('캐릭터명')} content={item?.user_profiles.character_name || '-'} />
								<DetailItem title={t('패밀리 네임')} content={item?.user_profiles.user_family_name || '-'} />
								<DetailItem title={t('캐릭터 성별')} content={item?.user_profiles.user_gender || '-'} />
								<DetailItem title={t('최고 랭킹 점수')} content={item?.user_profiles.best_ranking_point || 0} />
							</div>
						</div>
					</div>
					<p className='mb-2 text-base font-bold text-gray-700'>{t('업적')}</p>
					<div className='flex gap-10'>
						<div className='flex flex-col w-full'>
							<DetailItem title={t('총 누적 발화 성공')} content={item?.achievements.player_sr_count || 0} />
							<DetailItem title={t('누적 접속일')} content={`${item?.achievements.player_count_login || 0} days`} />
							<DetailItem title={t('달성 레벨')} content={`Level ${item?.achievements.player_level_up || 0}`} />
							<DetailItem title={t('스토리 모드 클리어 횟수')} content={item?.achievements.quest_count_clear || 0} />
							<DetailItem title={t('시간 여행 클리어')} content={item?.achievements.tt_count_clear || 0} />
							<DetailItem title={t('타임 트래블 Good 발화 횟수')} content={item?.achievements.tt_sr_count_good || 0} />
							<DetailItem title={t('타임 트래블 Nice 발화 횟수')} content={item?.achievements.tt_sr_count_nice || 0} />
							<DetailItem
								title={t('타임 트래블 Great 발화 횟수')}
								content={item?.achievements.tt_sr_count_great || 0}
							/>
							<DetailItem
								title={t('타임 트래블 Perfect 발화 횟수')}
								content={item?.achievements.tt_sr_count_perfect || 0}
							/>
							<DetailItem
								title={t('타임 트래블 Fantastic 발화 횟수')}
								content={item?.achievements.tt_sr_count_fantastic || 0}
							/>
							<DetailItem title={t('배틀맵 클리어 횟수')} content={item?.achievements.battle_map_count_clear || 0} />
						</div>

						<div className='flex flex-col w-full'>
							<DetailItem
								title={t('기억의 영화관 클리어 횟수')}
								content={item?.achievements.projector_count_clear || 0}
							/>
							<DetailItem
								title={t('캐주얼 톡 진행 횟수')}
								content={item?.achievements.npc_likability_count_talk || 0}
							/>
							<DetailItem title={t('워드 단어 완성 횟수')} content={item?.achievements.wordie_count_complete || 0} />
							<DetailItem title={t('청크 카드 총 발화 횟수')} content={item?.achievements.chunk_card_count_sr || 0} />
							<DetailItem
								title={t('청크 카드 레벨 업 횟수')}
								content={item?.achievements.chunk_card_count_level_up || 0}
							/>
							<DetailItem
								title={t('랜드마크 레벨 업 횟수')}
								content={item?.achievements.landmark_count_level_up || 0}
							/>
							<DetailItem title={t('랜드마크 알파벳 획득')} content={item?.achievements.landmark_count_collect || 0} />
							<DetailItem title={t('배틀맵 클리어 횟수')} content={item?.achievements.battle_map_count_clear || 0} />
							<DetailItem
								title={<Trans i18nKey={'건물 정화 횟수(링커 배틀 클리어 횟수)'} />}
								content={item?.achievements.linker_battle_count_clear || 0}
							/>
							<DetailItem title={t('누적 획득 루미')} content={`${item?.achievements.lumy_count_get || 0} Lumy`} />
							<DetailItem
								title={t('누적 오늘의 학습 지수')}
								content={item?.achievements.daily_mission_count_study_point || 0}
							/>
						</div>
					</div>
				</div>
			) : (
				<NoData image='non_actived_learner' label='학습자의 자세한 정보가 여기에 표시됩니다.' />
			)}
		</div>
	);
};

export default memo(
	LearnerDetail,
	(prev: LearnerDetailProps, next: LearnerDetailProps) => prev.activeUser === next.activeUser
);
