import { memo } from 'react';
import { IMAGE_SRC } from '../../constants/image';
import { cn } from '../../utils/style';
import { ButtonStyle, ImageStyle, LabelStyle } from './style';

interface ButtonProps {
	color: 'black' | 'blue' | 'white' | 'gray' | 'red' | 'white_line';
	size: 'lg' | 'md' | 'base' | 'sm';
	leftIcon?: string | JSX.Element;
	rightIcon?: string | JSX.Element;
	label?: string;
	disabled?: boolean;
	isFull?: boolean;
	className?: string;
	onClick: () => void;
}

const Button = ({
	color,
	size,
	leftIcon,
	rightIcon,
	label,
	disabled = false,
	isFull = false,
	className,
	onClick,
}: ButtonProps) => {
	return (
		<button
			type='button'
			className={`${cn(ButtonStyle({ color, size, iconOnly: !label ? size : null, isFull }))} ${className}`}
			disabled={disabled}
			onClick={onClick}
		>
			{leftIcon &&
				(typeof leftIcon === 'string' ? (
					<img
						className={cn(ImageStyle({ size, iconOnly: !label ? size : null }))}
						src={IMAGE_SRC[`${leftIcon}${disabled ? '_disabled' : ''}`]}
						alt={leftIcon}
					/>
				) : (
					<div className={cn(ImageStyle({ size, color, iconOnly: !label ? size : null, disabled }))}>{leftIcon}</div>
				))}
			{label && <span className={cn(LabelStyle({ color, size, disabled }))}>{label}</span>}
			{rightIcon &&
				(typeof rightIcon === 'string' ? (
					<img
						className={cn(ImageStyle({ size, iconOnly: !label ? size : null }))}
						src={IMAGE_SRC[`${rightIcon}${disabled ? '_disabled' : ''}`]}
						alt={rightIcon}
					/>
				) : (
					<div className={cn(ImageStyle({ size, color, iconOnly: !label ? size : null, disabled }))}>{rightIcon}</div>
				))}
		</button>
	);
};

export default memo(
	Button,
	(prev: ButtonProps, next: ButtonProps) =>
		prev.color === next.color &&
		prev.size === next.size &&
		prev.leftIcon === next.leftIcon &&
		prev.rightIcon === next.rightIcon &&
		prev.label === next.label &&
		prev.className === next.className &&
		prev.disabled === next.disabled &&
		prev.isFull === next.isFull &&
		prev.className === next.className &&
		prev.onClick === next.onClick
);
