import { t } from 'i18next';
import { useState } from 'react';
import Layout from '../../components/Layout/Layout';
import NoResult from '../../components/Modal/common/NoResult';
import SelectUser from '../../components/Modal/message/SelectUser';
import NoData from '../../components/NoData/NoData';
import { useUserListQuery } from '../../queries/authQuery';
import { useMessageIndividualListQuery } from '../../queries/messageQuery';
import useModal from '../../stores/useModal';
import MessageDetail from './components/MessageDetail';
import MessageList from './components/MessageList';
import SearchBar from './components/SearchBar';

const MessageIndividual = () => {
	const [keyword, setKeyword] = useState('');
	const [type, setType] = useState('learnerName');
	const [activeMessage, setActiveMessage] = useState({ id: 0, index: 0 });
	const [selectedUser, setSelectedUser] = useState<{ id: number; name: string }>({ id: 0, name: '' });
	const { openModal, closeModal } = useModal();

	const userList = useUserListQuery(type, keyword, (response) => {
		openModal({
			title: response.list.length ? t('수신자 선택') : t('결과 없음'),
			content: response.list.length ? (
				<SelectUser
					list={response.list}
					selectedUser={selectedUser}
					onConfirm={(value) => {
						setSelectedUser(value);
						closeModal();
					}}
				/>
			) : (
				<NoResult />
			),
			size: response.list.length ? 'lg' : 'sm',
		});
	});

	const messageIndividualList = useMessageIndividualListQuery(selectedUser.id, () => {
		setActiveMessage({ id: 0, index: 0 });
		closeModal();
	});
	const list = messageIndividualList.data?.list;

	const onSearch = () => {
		if (!keyword) {
			openModal({
				title: t('결과 없음'),
				content: <NoResult />,
				size: 'sm',
			});
		} else userList.refetch();
	};

	const onReset = () => {
		setKeyword('');
		setType('learnerName');
		setSelectedUser({ id: 0, name: '' });
	};

	return (
		<Layout>
			<SearchBar
				keyword={keyword}
				type={type}
				onSelect={(value: string) => setType(value)}
				onChange={(value: string) => setKeyword(value)}
				onReset={onReset}
				onSearch={onSearch}
			/>
			<div className='flex grow w-full h-[calc(100vh_-_188px)]'>
				{!list?.length ? (
					messageIndividualList.data ? (
						<NoData
							image='non_exist_message'
							label={t('[유저]에게 보낸 메시지가 없습니다.', { name: selectedUser.name })}
						/>
					) : (
						<NoData image='need_to_search' label={t('먼저 수신자를 검색해 주세요.')} />
					)
				) : (
					<>
						<MessageList
							list={list}
							name={selectedUser.name}
							activeMessage={activeMessage}
							setActiveMessage={setActiveMessage}
						/>
						<MessageDetail activeMessage={activeMessage} />
					</>
				)}
			</div>
		</Layout>
	);
};

export default MessageIndividual;
