import { Dispatch, SetStateAction } from 'react';
import Select from '../../../components/Select/Select';
import { TIME_TABLE, TimeTableType } from '../../../constants/messages';

interface TimePickerType {
	selectedTime: TimeTableType;
	setSelectedTime: Dispatch<SetStateAction<TimeTableType>>;
}

const TimePicker = ({ selectedTime, setSelectedTime }: TimePickerType) => {
	return (
		<Select
			size='lg'
			selected={selectedTime?.value}
			center={true}
			items={TIME_TABLE}
			onChange={(value) => {
				const index = TIME_TABLE.findIndex((time) => value === time.value);

				setSelectedTime(TIME_TABLE[index]);
			}}
		/>
	);
};

export default TimePicker;
