import { useTranslation } from 'react-i18next';
import Button from '../Button/Button';
import Input from '../Input/Input';

interface SearchProps {
	keyword: string;
	onChange: (value: string) => void;
	onSearch: () => void;
	onReset: () => void;
	placeholder: string;
}

const Search = ({ keyword, onChange, onSearch, onReset, placeholder }: SearchProps) => {
	const { t } = useTranslation();

	return (
		<div className='flex gap-2'>
			<div className='w-[320px]'>
				<Input size='sm' value={keyword} placeholder={t(placeholder)} onChange={onChange} onEnter={onSearch} />
			</div>
			<Button color='blue' size='md' label={t('검색')} onClick={onSearch} />
			{keyword && <Button color='white_line' size='md' label={t('초기화')} onClick={onReset} />}
		</div>
	);
};

export default Search;
