import { ReactNode, memo } from 'react';
import { cn } from '../../utils/style';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import SideBar from '../Sidebar/Sidebar';
import { LayoutStyle } from './style';

interface LayoutProps {
	children: ReactNode;
	sideBar?: boolean;
	title?: boolean;
	lang?: boolean;
	footer?: boolean;
}

const Layout = ({ children, sideBar = true, title = true, lang = false, footer = false }: LayoutProps) => {
	return (
		<div className='flex'>
			{sideBar && <SideBar />}
			<div className={cn(LayoutStyle({ sideBar }))}>
				<Header title={title} lang={lang} />
				{children}
				{footer && <Footer />}
			</div>
		</div>
	);
};

export default memo(
	Layout,
	(prev: LayoutProps, next: LayoutProps) =>
		prev.children === next.children &&
		prev.sideBar === next.sideBar &&
		prev.title === next.title &&
		prev.lang === next.lang &&
		prev.footer === next.footer
);
