import { IndividualMessageRegisterRequest, MessageRegisterRequest } from '../types/request';
import {
	MessageAllDetailResponse,
	MessageAllListResponse,
	MessageIndividualDetailResponse,
	MessageIndividualListResponse,
	MessageRegisterResponse,
} from '../types/response';
import axiosInstance from './axiosInstance';

const messageAllListAPI = async () => {
	const response = await axiosInstance.get<MessageAllListResponse>('/api/v1/event-post/list');

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const messageAllDetailAPI = async (id: number) => {
	const response = await axiosInstance.get<MessageAllDetailResponse>(`/api/v1/event-post/${id}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const messageIndividualListAPI = async (id: number) => {
	const response = await axiosInstance.get<MessageIndividualListResponse>(`/api/v1/send-post/list?userId=${id}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const messageIndividualDetailAPI = async (id: number) => {
	const response = await axiosInstance.get<MessageIndividualDetailResponse>(`/api/v1/send-post/${id}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const messageRegisterAllPostAPI = async (messageRegisterPayload: MessageRegisterRequest) => {
	const response = await axiosInstance.post<MessageRegisterResponse>('/api/v1/event-post', messageRegisterPayload);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const messageRegisterIndividualPostAPI = async (messageRegisterPayload: IndividualMessageRegisterRequest) => {
	const response = await axiosInstance.post<MessageRegisterResponse>('/api/v1/send-post', messageRegisterPayload);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

export {
	messageAllDetailAPI,
	messageAllListAPI,
	messageIndividualDetailAPI,
	messageIndividualListAPI,
	messageRegisterAllPostAPI,
	messageRegisterIndividualPostAPI,
};
