import { PencilIcon } from '@heroicons/react/24/solid';
import { useTranslation } from 'react-i18next';
import Button from '../../../components/Button/Button';
import Search from '../../../components/Search/Search';
import { useNavigate } from 'react-router-dom';
import { MESSAGE_SEND_TO_ALL_URL } from '../../../constants/router';

interface SearchBarProps {
	keyword: string;
	onChange: (value: string) => void;
	onReset: () => void;
	onSearch: () => void;
}

const SearchBar = ({ keyword, onChange, onReset, onSearch }: SearchBarProps) => {
	const { t } = useTranslation();

	const navigate = useNavigate();

	return (
		<div className='px-10 pb-6 border-b-[1px] border-gray-200 flex gap-2'>
			<Search
				keyword={keyword}
				onChange={onChange}
				onSearch={onSearch}
				onReset={onReset}
				placeholder='제목이나 내용을 입력해주세요.'
			/>
			<Button
				color='black'
				size='md'
				label={t('작성')}
				leftIcon={<PencilIcon />}
				className='ml-auto'
				onClick={() => {
					navigate(MESSAGE_SEND_TO_ALL_URL);
				}}
			/>
		</div>
	);
};

export default SearchBar;
