import { PaperClipIcon } from '@heroicons/react/24/outline';
import { ClockIcon } from '@heroicons/react/24/solid';
import { t } from 'i18next';
import { memo, useEffect, useRef } from 'react';
import NoData from '../../../components/NoData/NoData';
import { useMessageAllDetailQuery } from '../../../queries/messageQuery';
import { conversionDate } from '../../../utils/moment';

interface MessageDetailProps {
	activeMessage: { id: number; index: number };
}

const MessageDetail = ({ activeMessage }: MessageDetailProps) => {
	const ref = useRef<HTMLDivElement>(null);

	const messageDetail = useMessageAllDetailQuery(activeMessage.id);
	const item = messageDetail.item;

	useEffect(() => {
		ref.current?.scrollTo({ top: 0 });
	}, [activeMessage.id]);

	return (
		<div className='w-[600px] overflow-auto scroll-none' ref={ref}>
			{activeMessage.id ? (
				<div className='px-10 py-10'>
					<div className='flex gap-3 mb-4'>
						<div className='text-2xl font-bold text-gray-400'>{activeMessage.index}</div>
						<div className='text-2xl font-bold text-gray-900 break-all'>{item?.title}</div>
					</div>

					<div className='flex items-center gap-2 mb-12 text-base font-medium text-gray-400 min-h-[24px]'>
						<ClockIcon className='w-5 h-5' />
						<span className='text-gray-500'>
							{item?.create_time && conversionDate(item?.create_time, `[${t('보냄')}] YYYY-MM-DD HH:mm:ss`)}
						</span>
					</div>

					<div className='flex flex-col gap-1 mb-14'>
						<p className='text-base font-bold text-gray-800'>{t('전송 기간')}</p>
						<p className='flex gap-3 text-base font-medium text-gray-900 min-h-[24px]'>
							{item?.start_time && conversionDate(item?.start_time, 'YYYY-MM-DD HH:mm:ss')}
							{item?.end_time && conversionDate(item?.end_time, ' ~ YYYY-MM-DD HH:mm:ss')}
						</p>
					</div>

					<div className='flex flex-col gap-1 mb-14'>
						<p className='text-base font-bold text-gray-800'>{t('내용')}</p>
						<p className='text-base font-medium text-gray-900 break-all min-h-[24px]'>{item?.description}</p>
					</div>

					{!!item?.reward_count && (
						<div className='flex flex-col gap-1'>
							<p className='text-base font-bold text-gray-800'>{t('보상')}</p>
							<div className='py-3 px-5 bg-gray-50 rounded-[15px] justify-start items-center inline-flex gap-3 self-start'>
								<PaperClipIcon className='w-5 h-5 text-gray-400' />
								<span className='text-base font-medium text-gray-700'>{item?.reward_count} Lumy</span>
							</div>
						</div>
					)}
				</div>
			) : (
				<NoData image='non_actived_message' label='목록에서 메시지를 선택하면 여기에 세부 정보가 표시됩니다.' />
			)}
		</div>
	);
};

export default memo(
	MessageDetail,
	(prev: MessageDetailProps, next: MessageDetailProps) => prev.activeMessage === next.activeMessage
);
