import { useMemo, useState } from 'react';
import Layout from '../../components/Layout/Layout';
import NoData from '../../components/NoData/NoData';
import { useMessageAllListQuery } from '../../queries/messageQuery';
import MessageDetail from './components/MessageDetail';
import MessageList from './components/MessageList';
import SearchBar from './components/SearchBar';

const MessageAll = () => {
	const [keyword, setKeyword] = useState('');
	const [activeMessage, setActiveMessage] = useState({ id: 0, index: 0 });

	const messageAllList = useMessageAllListQuery();

	const filterList = useMemo(() => {
		const list = messageAllList.data?.list.filter(
			(item) => item.title.includes(keyword) || item.description.includes(keyword)
		);

		if (!list?.filter((item) => item.post_id === activeMessage.id).length) setActiveMessage({ id: 0, index: 0 });

		return list;
	}, [messageAllList.dataUpdatedAt]);

	const onSearch = () => {
		messageAllList.refetch();
	};

	const onReset = () => {
		setKeyword('');
		setActiveMessage({ id: 0, index: 0 });
		messageAllList.refetch();
	};

	return (
		<Layout>
			<SearchBar keyword={keyword} onChange={(value) => setKeyword(value)} onReset={onReset} onSearch={onSearch} />
			<div className='flex grow w-full h-[calc(100vh_-_188px)]'>
				{!filterList?.length ? (
					<NoData image='non_founded_data' label='검색 결과가 존재하지 않습니다.' />
				) : (
					<>
						<MessageList
							messageAllList={messageAllList.data?.list}
							filterList={filterList}
							activeMessage={activeMessage}
							setActiveMessage={setActiveMessage}
						/>
						<MessageDetail activeMessage={activeMessage} />
					</>
				)}
			</div>
		</Layout>
	);
};

export default MessageAll;
