import { ChangeEvent } from 'react';
import { t } from 'i18next';
import { preSignedUrlAPI } from '../../../apis/noticeApi';
import DatePicker from '../../../components/DatePicker/DatePicker';
import FileInput from '../../../components/FileInput/FileInput';
import Input from '../../../components/Input/Input';
import TextArea from '../../../components/TextArea/TextArea';
import useNoticeToast from '../../../hooks/useNoticeToast';
import { handleUpload } from '../../../utils/imageUpload';

import { NoticeInputValuesType } from '../../../types/notice';

interface NoticeEditInfoProps {
	inputValues: NoticeInputValuesType;
	handleNoticeInputValue: (type: string, value: NoticeInputValuesType[keyof NoticeInputValuesType]['value']) => void;
	handleNoticeInputError: (type: string, error: string) => void;
	handleInput: () => void;
}

const NoticeEditInfo = ({
	inputValues,
	handleNoticeInputValue,
	handleNoticeInputError,
	handleInput,
}: NoticeEditInfoProps) => {
	const { handleOpenSizeToast, handleOpenDimensionToast, handleOpenFormatToast } = useNoticeToast();

	const handleWarnFunc = (error: string, event: ChangeEvent<HTMLInputElement>) => {
		handleNoticeInputError('imageUploadName', t(error));
		handleNoticeInputValue('imageS3Url', '');
		handleNoticeInputValue('imageSrc', '');
		handleNoticeInputValue('imageUploadName', '');
		event.target.value = '';
	};

	const handleFileChange = async (event: ChangeEvent<HTMLInputElement>) => {
		handleNoticeInputError('imageUploadName', '');
		event.preventDefault();
		if (!event.target.files.length) return;

		const reader = new FileReader();
		const uploadedFile = event.target.files[0];

		const names = uploadedFile.name.split('.');
		const fileExtension = names[names.length - 1];

		if (uploadedFile.size > 500 * 1024) {
			handleOpenSizeToast();
			handleWarnFunc('이미지 최대 크기가 500KB를 초과합니다.', event);

			return;
		} else if (fileExtension.toLowerCase() !== 'png' && fileExtension.toLowerCase() !== 'jpg') {
			handleOpenFormatToast();
			handleWarnFunc('하나의 png 혹은 jpg 이미지만 등록이 가능합니다.', event);

			return;
		}

		const img = new Image();
		let width, height;

		img.onload = async () => {
			width = img.width;
			height = img.height;
			if (width !== 800 || height !== 250) {
				handleOpenDimensionToast();
				handleWarnFunc('800x250 크기만 가능합니다.', event);

				return;
			} else {
				const payload = {
					original_file_name: uploadedFile.name,
					file_size: uploadedFile.size,
					file_extension: fileExtension,
					service_id: 'betia',
					file_type: 'notice',
					custom_payload: {},
				};

				const preSignedRes = await preSignedUrlAPI(payload);
				const { read_url, hash_file_name } = preSignedRes.data;

				reader.onloadend = (e) => {
					let imageSrc = e.target.result;

					if (typeof imageSrc !== 'string') {
						const textDecoder = new TextDecoder('utf-8');
						imageSrc = textDecoder.decode(imageSrc);
					}

					handleUpload(preSignedRes.data, uploadedFile);

					handleNoticeInputValue('imageSrc', imageSrc);
					handleNoticeInputValue('imageS3Url', read_url + hash_file_name);
					handleNoticeInputValue('imageUploadName', uploadedFile.name);
				};

				reader.readAsDataURL(uploadedFile);
			}
		};

		img.src = URL.createObjectURL(uploadedFile);
	};

	return (
		<div className='w-[900px] border-r-[1px] h-full scroll-none overflow-auto px-10 pt-10 pb-20 border-gray-200'>
			<div className='flex flex-col justify-start max-w-[768px] gap-y-8'>
				<p className='text-xl font-bold text-gray-800 '>{t('공지사항 정보')}</p>
				<Input
					size='lg'
					label={
						<>
							<span className='text-red-600'>*</span> {t('제목')}
						</>
					}
					maxLength={30}
					value={inputValues.title.value}
					error={inputValues.title.error}
					placeholder={t('공지사항 제목을 입력해주세요.')}
					onChange={(value) => handleNoticeInputValue('title', value)}
					onError={(error) => handleNoticeInputError('title', error)}
					onEnter={handleInput}
					helper={<p>{t('공백을 포함한 최대 글자 수는 30입니다.')}</p>}
				/>
				<TextArea
					size='lg'
					label={
						<>
							<span className='text-red-600'>*</span> {t('내용')}
						</>
					}
					maxLength={1000}
					value={inputValues.description.value}
					error={inputValues.description.error}
					placeholder={t('공지사항 내용을 입력해주세요.')}
					height={336}
					onChange={(value) => handleNoticeInputValue('description', value)}
					onError={(error) => handleNoticeInputError('description', error)}
					onEnter={handleInput}
					helper={<p>{t('공백을 포함한 최대 글자 수는 1000입니다.')}</p>}
				/>
				<FileInput
					size='lg'
					label={
						<>
							<span className='text-red-600'>*</span> {t('이미지 업로드')}
						</>
					}
					buttonName={t('파일 선택')}
					value={inputValues.imageUploadName.value}
					error={inputValues.imageUploadName.error}
					placeholder={t('선택된 파일 없음')}
					helper={
						<>
							<p>{t('이미지 파일은 800x250 크기이고 500KB 미만이어야 합니다.')}</p>
							<p>{t('하나의 png 혹은 jpg 이미지만 등록이 가능합니다.')}</p>
						</>
					}
					onError={(error) => handleNoticeInputError('imageUploadName', error)}
					onChange={(e) => handleFileChange(e)}
					onEnter={handleInput}
				/>
				<Input
					size='lg'
					label={<>{t('URL 링크')}</>}
					value={inputValues.urlLink.value}
					error={inputValues.urlLink.error}
					placeholder={t('URL 링크를 입력해주세요.')}
					onChange={(value) => handleNoticeInputValue('urlLink', value)}
					onError={(error) => handleNoticeInputError('urlLink', error)}
					onEnter={handleInput}
				/>
				<DatePicker
					label={
						<>
							<span className='text-red-600'>*</span>
							{t('노출 기간 범위')}
						</>
					}
					value={Object(inputValues.date.value)}
					error={inputValues.date.error}
					onChange={(value) =>
						handleNoticeInputValue('date', {
							startDate: value.startDate + 'T00:00:00.945Z',
							endDate: value.endDate + 'T23:59:59.945Z',
						})
					}
					onError={(error) => handleNoticeInputError('date', error)}
					onEnter={handleInput}
				/>
			</div>
		</div>
	);
};

export default NoticeEditInfo;
