import { useTranslation } from 'react-i18next';
import Input from '../../../components/Input/Input';
import Button from '../../../components/Button/Button';

interface SearchProps {
	keyword: string;
	onChange: (value: string) => void;
	onSearch: () => void;
	onReset: () => void;
	placeholder: string;
	error?: string;
	onError?: (error: string) => void;
}

const Search = ({ keyword, onChange, onSearch, onReset, placeholder, error, onError }: SearchProps) => {
	const { t } = useTranslation();

	return (
		<div>
			<div className='flex gap-2'>
				<div className='w-[320px]'>
					<Input
						onError={onError}
						error={error}
						size='lg'
						value={keyword}
						placeholder={t(placeholder)}
						onChange={onChange}
						onEnter={onSearch}
					/>
				</div>
				<Button className='h-[52px]' color='blue' size='md' label={t('검색')} onClick={onSearch} />
				{keyword && <Button className='h-[52px]' color='white_line' size='md' label={t('초기화')} onClick={onReset} />}
			</div>
		</div>
	);
};

export default Search;
