import { memo } from 'react';
import { MessageIndividualListType } from '../../../../types/messages';
import { cn } from '../../../../utils/style';
import { ItemStyle } from './style';

interface ItemProps {
	item: MessageIndividualListType;
	activeMessage: { id: number; index: number };
	onClick: (id: number, index: number) => void;
}

const ListItem = ({ item, activeMessage, onClick }: ItemProps) => {
	return (
		<li
			className={cn(ItemStyle({ active: item.post_id === activeMessage.id }))}
			onClick={() => onClick(item.post_id, item.index)}
		>
			<div className='text-base text-gray-900'>{item.index}</div>
			<div className='grow'>
				<p className='mb-2 text-base text-gray-900 break-all text-ellipsis-1'>{item.title}</p>
				<p className='mb-1 text-sm text-gray-500 break-all text-ellipsis-1'>{item.description}</p>
			</div>
			<div className='flex-none inline-block mt-4 ml-auto'>{item.registered}</div>
		</li>
	);
};

export default memo(
	ListItem,
	(prev: ItemProps, next: ItemProps) =>
		prev.item === next.item && prev.activeMessage === next.activeMessage && prev.onClick === next.onClick
);
