import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { LanguageType } from '../types/language';

interface UseLanguageType {
	language: LanguageType;
	prevLanguage: LanguageType;
	setLanguage: (value: LanguageType) => void;
	setPrevLanguage: (value: LanguageType) => void;
}

const useLanguage = create(
	persist<UseLanguageType>(
		(set) => ({
			language: 'en',
			prevLanguage: 'en',
			setLanguage: (value) => set({ language: value }),
			setPrevLanguage: (value) => set({ prevLanguage: value }),
		}),
		{
			name: 'language',
		}
	)
);

export default useLanguage;
