import { t } from 'i18next';
import { Dispatch, SetStateAction, memo } from 'react';
import { MessageIndividualListType } from '../../../types/messages';
import ListItem from './ListItem/ListItem';

interface MessageListProps {
	list: MessageIndividualListType[];
	name: string;
	activeMessage: { id: number; index: number };
	setActiveMessage: Dispatch<SetStateAction<{ id: number; index: number }>>;
}

const MessageList = ({ list, name, activeMessage, setActiveMessage }: MessageListProps) => {
	const handleActiveMessage = (id: number, index: number) => {
		setActiveMessage({ id: id, index: index });
	};

	return (
		<div className='w-[calc(100%_-_600px)] h-full px-10 py-10 border-r-[1px] border-gray-200 overflow-auto scroll-none'>
			<div className='mb-4 text-base font-medium text-gray-500'>{t('[유저]의 받은 편지함', { name: name })}</div>
			<ul>
				{list.map((item) => (
					<ListItem key={item.post_id} item={item} activeMessage={activeMessage} onClick={handleActiveMessage} />
				))}
			</ul>
		</div>
	);
};

export default memo(
	MessageList,
	(prev: MessageListProps, next: MessageListProps) =>
		prev.list === next.list &&
		prev.name === next.name &&
		prev.activeMessage === next.activeMessage &&
		prev.setActiveMessage === next.setActiveMessage
);
