import { useLocation } from 'react-router-dom';
import { SITE_MAP } from '../../constants/router';
import BreadCrumbs from '../BreadCrumbs/BreadCrumbs';
import { t } from 'i18next';

const Title = () => {
	const location = useLocation();

	return (
		<div className='px-2 pt-7 pb-9'>
			{!!SITE_MAP[location.pathname].bread_crumbs.length && <BreadCrumbs />}
			<p className='text-2xl font-bold leading-snug text-gray-800'>{t(SITE_MAP[location.pathname].label)}</p>
		</div>
	);
};

export default Title;
