import { t } from 'i18next';
import moment from 'moment';

const conversionDate = (date: string, format: string) => {
	return moment(date).format(format);
};

const sortAscending = (prevDate: string, nextDate: string) => {
	return moment(nextDate).diff(moment(prevDate));
};

const checkTodayInRange = (prevDate: string, nextDate: string) => {
	return moment().isBetween(moment(prevDate), moment(nextDate));
};

const getLeftDateFromToday = (date: string) => {
	const today = moment(moment().format('YYYY-MM-DD'));
	const targetDate = moment(date);
	const diffInDays = targetDate.diff(today, 'days') + 1;

	if (diffInDays < 1) return 0;
	else return diffInDays;
};

const getRegisteredDate = (date: string) => {
	const today = moment();
	const targetDate = moment(date);
	const diffInDays = today.diff(targetDate, 'days');

	if (diffInDays === 0) return t('오늘');
	else if (diffInDays === 1) return t('어제');
	else if (diffInDays >= 2 && diffInDays <= 30) return `${diffInDays} ${t('일 전')}`;
	else return targetDate.format('YYYY-MM-DD');
};

const mergeDateAndHour = (date: string, hour: string) => {
	const desiredDate = moment(date);

	const desiredTime = moment(hour, 'HH:mm');

	return desiredDate
		.set({
			hour: desiredTime.hour(),
			minute: desiredTime.minute(),
		})
		.toDate(); // JavaScript Date 객체로 변환
};

export { checkTodayInRange, conversionDate, getLeftDateFromToday, getRegisteredDate, mergeDateAndHour, sortAscending };
