import { memo, useEffect } from 'react';
import Datepicker, { DateValueType, DatepickerType } from 'react-tailwindcss-datepicker';
import { cn } from '../../utils/style';
import { InputStyle, LabelStyle, ToggleStyle } from './style';
import { t } from 'i18next';

export type DatePickerType = {
	startDate: string;
	endDate: string;
};

interface DatePickerProps extends DatepickerType {
	label?: JSX.Element;
	error?: string;
	value: DateValueType;
	onChange: (value: DateValueType) => void;
	onError?: (error: string) => void;
	onEnter?: () => void;
}

const DatePicker = ({ label, value, error, onChange, onError, ...props }: DatePickerProps) => {
	useEffect(() => {
		if (!onError) return;

		onError('');
	}, [value]);

	return (
		<div className='flex flex-col'>
			{label && <label className={cn(LabelStyle({ error: Boolean(error) }))}>{label}</label>}
			<Datepicker
				toggleClassName={cn(ToggleStyle({ error: Boolean(error) }))}
				inputClassName={cn(InputStyle({ error: Boolean(error) }))}
				{...props}
				value={value}
				placeholder={t('날짜를 선택해주세요.')}
				onChange={onChange}
				showShortcuts={true}
			/>
			{error && <p className='pt-2 text-sm font-medium leading-5 text-red-600 '>{error}</p>}
		</div>
	);
};

export default memo(
	DatePicker,
	(prev: DatePickerProps, next: DatePickerProps) =>
		prev.label === next.label &&
		prev.value === next.value &&
		prev.error === next.error &&
		prev.value === next.value &&
		prev.onError === next.onError &&
		prev.onEnter === next.onEnter
);
