import { ChangeEvent, memo, useEffect } from 'react';
import { cn } from '../../utils/style';
import { ButtonStyle, HelperStyle, InputLabelStyle, InputTextStyle, LabelStyle } from './style';

interface FileInputProps {
	size: 'lg' | 'sm';
	label?: JSX.Element;
	buttonName: string;
	value: string;
	error?: string;
	placeholder?: string;
	helper?: JSX.Element;
	password?: string;
	onError?: (error: string) => void;
	onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
	onEnter?: () => void;
}

const FileInput = ({
	size,
	label,
	buttonName,
	onEnter,
	value,
	error,
	placeholder,
	helper,
	onError,
	onChange,
}: FileInputProps) => {
	useEffect(() => {
		if (error) return;

		onError('');
	}, [value, error]);

	const getStatus = (value: string | null) => {
		if (value) {
			if (error) {
				return 'isValueError';
			}
			return 'isValueNotError';
		} else {
			if (error) {
				return 'isPlaceholderError';
			}
			return 'isPlaceholderNotError';
		}
	};

	return (
		<div className='w-full'>
			{label && <label className={cn(LabelStyle({ size, error: Boolean(error) }))}>{label}</label>}
			<div className='relative flex w-full group'>
				<button className={cn(ButtonStyle({ size }))}>{buttonName}</button>
				<label className={cn(InputLabelStyle({ size, error: Boolean(error) }))}>
					<span className={cn(InputTextStyle({ status: getStatus(value) }))}>{value ? value : placeholder}</span>
				</label>
				<input
					type='file'
					onKeyDown={(event) => event.key === 'Enter' && onEnter && onEnter()}
					onChange={(e) => onChange(e)}
					className='absolute top-0 left-0 w-full h-full opacity-0 cursor-pointer z-1'
				></input>
			</div>
			{helper && <div className={cn(HelperStyle({ size, error: Boolean(error) }))}>{helper}</div>}
		</div>
	);
};

export default memo(
	FileInput,
	(prev: FileInputProps, next: FileInputProps) =>
		prev.size === next.size &&
		prev.label === next.label &&
		prev.buttonName === next.buttonName &&
		prev.value === next.value &&
		prev.error === next.error &&
		prev.placeholder === next.placeholder &&
		prev.helper === next.helper &&
		prev.password === next.password &&
		prev.onChange === next.onChange &&
		prev.onEnter === next.onEnter &&
		prev.onError === next.onError
);
