import { t } from 'i18next';
import { useQuery } from 'react-query';
import { loginAPI, userDetailAPI, userListAPI } from '../apis/authApi';
import { LoginResponse, UserDetailResponse, UserListResponse } from '../types/response';
import { sortAscending } from '../utils/moment';

const useLoginQuery = (username: string, password: string, onSuccess: () => void, onError: (error: number) => void) => {
	const query = useQuery<LoginResponse, number>(['login'], () => loginAPI(username, password), {
		enabled: false,
		onSuccess,
		onError,
	});

	return query;
};

const useUserListQuery = (type: string, keyword: string, onSuccess?: (response: UserListResponse) => void) => {
	const query = useQuery<UserListResponse, number>(['userList'], () => userListAPI(type, keyword), {
		enabled: false,
		staleTime: 0,
		cacheTime: 0,
		onSuccess,
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
		select: (data: UserListResponse) => {
			const selectData = [...data.list.sort((a, b) => sortAscending(a.create_time, b.create_time))];

			return {
				...data,
				list: selectData,
			};
		},
	});

	return query;
};

const useUserDetailQuery = (id: number) => {
	const query = useQuery<UserDetailResponse, number>(['userDetail', id], () => userDetailAPI(id), {
		enabled: !!id,
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
	});

	const item = query.data?.data;

	return { ...query, item };
};

const useUserListQuery2 = (type: string, keyword: string, onSuccess: (response: UserListResponse) => void) => {
	const query = useQuery<UserListResponse, number>(['userList2'], () => userListAPI(type, keyword), {
		enabled: false,
		onSuccess,
		onError: () => alert(t('알 수 없는 문제가 발생하였습니다.')),
	});

	return query;
};

export { useLoginQuery, useUserDetailQuery, useUserListQuery, useUserListQuery2 };
