import { QuestionMarkCircleIcon } from '@heroicons/react/24/solid';
import { t } from 'i18next';
import DatePicker, { DatePickerType } from '../../../components/DatePicker/DatePicker';
import { TimeTableType } from '../../../constants/messages';
import TimePicker from './TimePicker';
import Title from './Title';
import { Dispatch, SetStateAction } from 'react';

type DateType = { startDate: string; endDate: string };
type DateObject = {
	onChange: (newValue: DatePickerType) => void;
	value: DateType;
	error: string;
	onError: (error: string) => void;
};
type TimeObject = { selectedTime: TimeTableType; setSelectedTime: Dispatch<SetStateAction<TimeTableType>> };

interface DateSettingBox {
	date: { startDate: DateObject; endDate: DateObject };
	time: { startTime: TimeObject; endTime: TimeObject };
}

const DateSettingBox = ({ date, time }: DateSettingBox) => {
	const { startDate, endDate } = date;
	const { startTime, endTime } = time;

	return (
		<div>
			<Title className='pb-4'>{t('전송 기간')}</Title>
			<div className='flex flex-col gap-2 pb-4'>
				<p>{t('시작 날짜')}</p>
				<div className='flex gap-2'>
					<div className='w-[320px]'>
						<DatePicker
							useRange={false}
							asSingle={true}
							placeholder={t('날짜 선택')}
							onChange={startDate.onChange}
							value={startDate.value}
							error={startDate.error}
							onError={startDate.onError}
						></DatePicker>
					</div>
					<div className='flex-[1]'>
						<TimePicker selectedTime={startTime.selectedTime} setSelectedTime={startTime.setSelectedTime}></TimePicker>
					</div>
				</div>
			</div>
			<div className='flex flex-col gap-2 pb-4 mb-8'>
				<p>{t('종료 날짜')}</p>
				<div className='flex gap-2'>
					<div className='w-[320px]'>
						<DatePicker
							asSingle={true}
							useRange={false}
							placeholder={t('날짜 선택')}
							onChange={endDate.onChange}
							value={endDate.value}
							error={endDate.error}
							onError={endDate.onError}
						></DatePicker>
					</div>
					<div className='flex-[1]'>
						<TimePicker selectedTime={endTime.selectedTime} setSelectedTime={endTime.setSelectedTime}></TimePicker>
					</div>
				</div>
			</div>
			<div className='flex items-center w-full h-fit'>
				<QuestionMarkCircleIcon className='w-[16px] h-[16px] mr-1' />
				<p className='w-full text-xs font-medium leading-4 text-gray-500'>
					{t('만료일 : 메일 수령 후 보상을 청구할 수 있는 기간은 29일입니다.')}
				</p>
			</div>
		</div>
	);
};

export default DateSettingBox;
