import { LoginResponse, UserDetailResponse, UserListResponse } from '../types/response';
import axiosInstance from './axiosInstance';

const loginAPI = async (username: string, password: string) => {
	const response = await axiosInstance.post<LoginResponse>('/api/v1/login', {
		login_id: username,
		password: password,
	});

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const userListAPI = async (type: string, keyword: string) => {
	const response = await axiosInstance.get<UserListResponse>(`/api/v1/users/list?type=${type}&keyword=${keyword}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

const userDetailAPI = async (id: number) => {
	const response = await axiosInstance.get<UserDetailResponse>(`/api/v1/users/${id}`);

	if (response.status === 200) return response.data;
	else throw response.data.code;
};

export { loginAPI, userDetailAPI, userListAPI };
