import { Dispatch, SetStateAction, useState } from 'react';
import { useTranslation } from 'react-i18next';
import useModal from '../../../stores/useModal';
import { UserListType } from '../../../types/auth';
import Button from '../../Button/Button';
import Radio from '../../Radio/Radio';

interface SelectRecipientProps {
	list: UserListType[];
	selectedUser: { id: string; name: string };
	setSelectedUser: Dispatch<SetStateAction<{ id: string; name: string }>>;
	onConfirm: (value: { id: string; name: string }) => void;
}

const SelectRecipient = ({ list, selectedUser, onConfirm }: SelectRecipientProps) => {
	const { closeModal } = useModal();
	const { t } = useTranslation();
	const [selected, setSelected] = useState<{ id: string; name: string }>(selectedUser);

	return (
		<div>
			<div className='w-full mb-8'>
				<p className='flex gap-2 mb-5 text-base font-bold'>
					<span className='text-gray-800'>{t('검색 결과')}</span> <span className='text-gray-400'>{list?.length}</span>
				</p>

				<div className='table w-full text-sm font-medium '>
					<div className='flex items-center w-full h-10 text-gray-600 bg-gray-100 th'>
						<div className='td w-[10%]'></div>
						<div className='td w-[35%] px-3'>{t('이름')}</div>
						<div className='td w-[55%] px-3'>
							{t('유저 아이디')} ({t('이메일 주소')})
						</div>
					</div>
					<div className='overflow-auto text-gray-800 h-[218px] scroll-none'>
						{list.map((item) => (
							<div
								key={item.user_id}
								className='h-12 tr border-b-[1px] border-gray-200 flex items-center cursor-pointer'
								onClick={() => setSelected({ id: String(item.user_id), name: item.learner_name })}
							>
								<div className='flex items-center justify-center td w-[10%] h-full'>
									<Radio id={String(item.user_id)} selected={selected.id} />
								</div>
								<div className='td w-[35%] px-3 break-all text-ellipsis-1'>{item.learner_name}</div>
								<div className='td w-[55%] px-3 break-all text-ellipsis-1'>{item.login_id}</div>
							</div>
						))}
					</div>
				</div>
			</div>
			<div className='flex gap-3'>
				<Button color='gray' size='lg' isFull={true} label={t('취소')} onClick={closeModal} />
				<Button
					color='black'
					size='lg'
					isFull={true}
					label={t('선택')}
					onClick={() => {
						onConfirm(selected);
					}}
				/>
			</div>
		</div>
	);
};

export default SelectRecipient;
