import { memo } from 'react';
import { Trans } from 'react-i18next';
import { IMAGE_SRC } from '../../constants/image';

interface NoDataProps {
	image: string;
	label: string;
}

const NoData = ({ image, label }: NoDataProps) => {
	return (
		<div className='relative w-full h-full'>
			<div className='flex flex-col items-center justify-center gap-4 text-base font-medium position-center'>
				<img src={IMAGE_SRC[image]} alt='no results' className='w-[120px] h-[120px]' />
				<p className='text-base font-medium text-center text-gray-500 w-[320px]'>
					<Trans i18nKey={label} className='text-base font-medium text-center text-gray-500' />
				</p>
			</div>
		</div>
	);
};

export default memo(
	NoData,
	(prev: NoDataProps, next: NoDataProps) => prev.image === next.image && prev.label === next.label
);
