import { t } from 'i18next';
import { useMemo, useState } from 'react';
import Layout from '../../components/Layout/Layout';
import NoData from '../../components/NoData/NoData';
import { useUserListQuery } from '../../queries/authQuery';
import LearnerDetail from './components/LearnerDetail';
import LearnerList from './components/LearnerList';
import SearchBar from './components/SearchBar';

const LearnerInfo = () => {
	const [keyword, setKeyword] = useState('');
	const [type, setType] = useState('learnerName');
	const [activeUser, setActiveUser] = useState(0);

	const userList = useUserListQuery(type, keyword);

	const filterList = useMemo(() => {
		const list = userList.data?.list.filter((item) => {
			if (type === 'learnerName') return item.learner_name.includes(keyword);
			else if (type === 'loginId') return item.login_id.includes(keyword);
		});

		if (!list?.filter((item) => item.user_id === activeUser).length) setActiveUser(0);

		return list;
	}, [userList.dataUpdatedAt]);

	const onSearch = () => {
		if (!keyword) {
			alert(t('학습자 이름 또는 사용자 ID를 입력해주세요.'));
			return false;
		}

		userList.refetch();
	};

	const onReset = () => {
		setKeyword('');
		setType('learnerName');
		setActiveUser(0);
		userList.remove();
	};

	return (
		<Layout>
			<SearchBar
				keyword={keyword}
				type={type}
				onSelect={(value: string) => setType(value)}
				onChange={(value: string) => setKeyword(value)}
				onReset={onReset}
				onSearch={onSearch}
			/>
			<div className='flex grow w-full h-[calc(100vh_-_188px)]'>
				{!filterList?.length ? (
					userList.data ? (
						<NoData image='non_exist_message' label={t('검색 결과가 없습니다. 다른 키워드로 검색하세요.')} />
					) : (
						<NoData image='need_to_search' label={t('먼저 학습자 정보를 검색하세요.')} />
					)
				) : (
					<>
						<LearnerList filterList={filterList} activeUser={activeUser} setActiveUser={setActiveUser} />
						<LearnerDetail activeUser={activeUser} />
					</>
				)}
			</div>
		</Layout>
	);
};

export default LearnerInfo;
