import { ClipboardIcon } from '@heroicons/react/24/solid';
import { t } from 'i18next';
import { memo } from 'react';
import { useNavigate } from 'react-router-dom';
import Button from '../../../components/Button/Button';
import Search from '../../../components/Search/Search';
import { NOTICE_REGISTRATION_URL } from '../../../constants/router';

interface SearchBarProps {
	keyword: string;
	onChange: (value: string) => void;
	onReset: () => void;
	onSearch: () => void;
}

const SearchBar = ({ keyword, onChange, onReset, onSearch }: SearchBarProps) => {
	const navigate = useNavigate();

	return (
		<div className='px-10 pb-6 border-b-[1px] border-gray-200 flex gap-2'>
			<Search
				keyword={keyword}
				placeholder='제목이나 키워드를 입력해주세요.'
				onChange={onChange}
				onSearch={onSearch}
				onReset={onReset}
			/>
			<Button
				color='black'
				size='md'
				label={t('공지사항 작성하기')}
				leftIcon={<ClipboardIcon />}
				className='ml-auto'
				onClick={() => navigate(NOTICE_REGISTRATION_URL)}
			/>
		</div>
	);
};

export default memo(
	SearchBar,
	(prev: SearchBarProps, next: SearchBarProps) =>
		prev.keyword === next.keyword &&
		prev.onChange === next.onChange &&
		prev.onReset === next.onReset &&
		prev.onSearch === next.onSearch
);
