import { PencilIcon } from '@heroicons/react/24/outline';
import { TrashIcon } from '@heroicons/react/24/solid';
import { t } from 'i18next';
import { memo, useEffect, useRef, useState } from 'react';
import { Trans } from 'react-i18next';
import { useQueryClient } from 'react-query';
import { Link, useNavigate } from 'react-router-dom';
import Badge from '../../../components/Badge/Badge';
import Button from '../../../components/Button/Button';
import ImagePreview from '../../../components/ImagePreview/ImagePreview';
import UndoPosting from '../../../components/Modal/notice/UndoPosting';
import NoData from '../../../components/NoData/NoData';
import { NOTICE_EDIT_URL } from '../../../constants/router';
import { useNoticeUndoMutation, useNoticeDetailQuery } from '../../../queries/noticeQuery';
import useModal from '../../../stores/useModal';
import useToast from '../../../stores/useToast';
import { conversionDate } from '../../../utils/moment';

interface NoticeDetailProps {
	activeNotice: { id: string; index: number };
}

const NoticeDetail = ({ activeNotice }: NoticeDetailProps) => {
	const navigate = useNavigate();
	const ref = useRef<HTMLDivElement>(null);
	const queryClient = useQueryClient();
	const [isImageError, setIsImageError] = useState(false);
	const { openModal, closeModal } = useModal();
	const { openToast } = useToast();

	const noticeDetail = useNoticeDetailQuery(activeNotice.id);
	const item = noticeDetail.item;

	const noticeUndo = useNoticeUndoMutation(() => {
		queryClient.invalidateQueries('noticeList');
		queryClient.invalidateQueries('noticeDetail');
		closeModal();
		openToast({
			toastingTime: 5000,
			title: t('공지 게시가 취소되었습니다.'),
			leftIcon: 'toast_success',
			position: 'center',
		});
	});

	const handleUndoPost = (id: string) => {
		openModal({
			title: t('게시 취소'),
			subTitle: <Trans i18nKey='공지사항 게시를 취소하시겠습니까? 게임에서 삭제됩니다.' />,
			content: <UndoPosting onUndo={() => noticeUndo.mutate({ id: id })} />,
			size: 'sm',
		});
	};

	useEffect(() => {
		ref.current?.scrollTo({ top: 0 });
	}, [activeNotice.id]);

	return (
		<div className='w-[750px] overflow-auto scroll-none' ref={ref}>
			{activeNotice.id ? (
				<div className='px-10 py-10'>
					<div className='flex gap-3 mb-4'>
						<div className='mt-1 text-2xl font-bold text-gray-400'>{activeNotice.index}</div>
						<div className='flex gap-3'>
							<div className='flex items-center text-2xl font-bold text-gray-900 break-all grow'>{item?.title}</div>
							{item?.exposure && <Badge color='blue' size='sm' label={t('게시')} className='self-start mt-2' />}
						</div>
						<div className='flex self-start flex-none gap-3 ml-auto'>
							{item?.exposure && (
								<Button
									color='red'
									size='md'
									leftIcon={<TrashIcon />}
									label={t('게시 취소')}
									onClick={() => handleUndoPost(activeNotice.id)}
								/>
							)}
							<Button
								color='white_line'
								size='md'
								leftIcon={<PencilIcon />}
								label={t('수정')}
								onClick={() => navigate(NOTICE_EDIT_URL, { state: activeNotice.id })}
							/>
						</div>
					</div>

					<div className='mb-12 text-sm text-gray-500'>
						<p className='mb-1'>
							{t('노출 날짜')} {item?.start_time && conversionDate(item?.start_time, 'YYYY-MM-DD')}
							{item?.end_time && `${' - ' + conversionDate(item?.end_time, 'YYYY-MM-DD')}`}
						</p>
						<p className='mb-1'>
							{t('등록 날짜')} {item?.registered}
						</p>
						<p>
							{t('수정 날짜')} {item?.update_time && conversionDate(item?.update_time, 'YYYY-MM-DD HH:mm:ss')}
						</p>
					</div>

					<div className='mb-12'>
						<p className='mb-2 text-base font-bold text-gray-800'>{t('미리 보기')}</p>
						<ImagePreview title={item?.title} body={item?.body} imageUrl={item?.img_url} />
					</div>

					<div className='mb-12'>
						<p className='mb-2 text-base font-bold text-gray-800'>{t('내용')}</p>
						<pre className='text-base font-medium text-gray-900 break-words whitespace-break-spaces font-noto'>
							{item?.body}
						</pre>
					</div>

					<div className={item?.link ? 'mb-12' : ''}>
						<p className='mb-2 text-base font-bold text-gray-800'>{t('업로드 된 이미지')}</p>
						<div className='relative'>
							{isImageError && (
								<div className='absolute top-0 left-0 w-full h-full text-base font-medium text-gray-900 bg-white'>
									{t('정상적인 이미지 경로가 아닙니다.')}
								</div>
							)}
							<img
								src={item?.img_url}
								alt='notice_image'
								className='w-[100%]'
								onLoad={() => setIsImageError(false)}
								onError={() => setIsImageError(true)}
							/>
						</div>
					</div>

					{item?.link && (
						<div>
							<p className='mb-2 text-base font-bold text-gray-800'>URL Link</p>
							<Link to={item?.link} target='_blank' className='underline '>
								{item?.link}
							</Link>
						</div>
					)}
				</div>
			) : (
				<NoData image='non_actived_notice' label='공지사항에 대한 자세한 정보는 여기에 표시됩니다.' />
			)}
		</div>
	);
};

export default memo(
	NoticeDetail,
	(prev: NoticeDetailProps, next: NoticeDetailProps) => prev.activeNotice === next.activeNotice
);
