import { memo, useState } from 'react';
import { IMAGE_SRC } from '../../constants/image';
import { cn } from '../../utils/style';
import { TitleStyle } from './style';

interface ImagePreviewProps {
	title: string;
	body: string;
	imageUrl: string;
}

const ImagePreview = ({ title, body, imageUrl }: ImagePreviewProps) => {
	const [isImageError, setIsImageError] = useState(false);

	return (
		<div className='relative w-[546px] h-[350px]'>
			<img src={IMAGE_SRC.notice_popup} alt='notice_popup' />
			<div className={`${cn(TitleStyle({ center: title?.length < 13 }))} text-stroke-title`}>{title}</div>
			<div className='absolute w-[401px] h-[129px] top-[38px] left-[129px] overflow-hidden'>
				<div className='relative'>
					{isImageError && (
						<div className='absolute top-0 left-0 w-[410px] h-[128px] bg-[#495D86]'>
							<img src={IMAGE_SRC.error_image} alt='error 404' className='w-[180px] position-center' />
						</div>
					)}
					{imageUrl && (
						<img
							src={imageUrl}
							alt='notice_image'
							onLoad={() => setIsImageError(false)}
							onError={() => setIsImageError(true)}
						/>
					)}
				</div>
			</div>
			<pre className='absolute font-noto w-[398px] h-[120px] bottom-[52px] left-[128px] text-white text-[12px] font-medium tracking-wider overflow-scroll scroll-none whitespace-break-spaces break-words text-stroke-body'>
				{body}
			</pre>
		</div>
	);
};

export default memo(
	ImagePreview,
	(prev: ImagePreviewProps, next: ImagePreviewProps) =>
		prev.title === next.title && prev.body === next.body && prev.imageUrl === next.imageUrl
);
