import { ChevronDownIcon } from '@heroicons/react/24/solid';
import { memo, useState } from 'react';
import { cn } from '../../utils/style';
import { ArrowStyle, ButtonStyle, LabelStyle, SelectedStyle } from './style';

interface SelectItem {
	value: string;
	label: string;
}

interface SelectProps {
	size: 'sm' | 'md' | 'lg';
	selected: string;
	center?: boolean;
	label?: string;
	placeholder?: string;
	items: SelectItem[];
	onChange: (value: string) => void;
}

const Select = ({ size, selected, center = false, label, placeholder, items, onChange }: SelectProps) => {
	const [isOpen, setIsOpen] = useState(false);

	const getLabel = (items: SelectItem[], selected: string) => {
		return items.filter((item) => item.value === selected)[0]?.label;
	};

	return (
		<div className='relative flex flex-col gap-2'>
			{label && <p className={cn(LabelStyle({ size }))}>{label}</p>}
			<button type='button' onClick={() => setIsOpen((state) => !state)} className={cn(SelectedStyle({ size }))}>
				{selected ? getLabel(items, selected) : placeholder}
				<ChevronDownIcon className={cn(ArrowStyle({ size }))} />
			</button>
			{isOpen && (
				<div className='absolute z-10 w-full rounded-lg font-medium bg-white border shadow-border bottom-[-8px] translate-y-[100%] max-h-[320px] overflow-scroll scroll-none'>
					<ul className='flex flex-col p-1 text-base text-gray-700'>
						{items.map((item) => (
							<li
								key={item.value}
								onClick={() => {
									onChange(item.value);
									setIsOpen(false);
								}}
							>
								<button className={cn(ButtonStyle({ center }))}>{item.label}</button>
							</li>
						))}
					</ul>
				</div>
			)}
		</div>
	);
};

export default memo(
	Select,
	(prev: SelectProps, next: SelectProps) =>
		prev.size === next.size &&
		prev.selected === next.selected &&
		prev.center === next.center &&
		prev.label === next.label &&
		prev.placeholder === next.placeholder &&
		prev.items === next.items &&
		prev.onChange === next.onChange
);
