import { useState } from 'react';
import Layout from '../../components/Layout/Layout';
import { useNavigate } from 'react-router-dom';
import Input from '../../components/Input/Input';
import { InputValueType } from '../../types/common';
import useInput from '../../hooks/useInput';
import Title from './components/Title';
import { TimeTableType } from '../../constants/messages';
import { DatePickerType } from '../../components/DatePicker/DatePicker';
import { useMessageAllRegisterMutation } from '../../queries/messageQuery';
import { MESSAGES_ALL_URL } from '../../constants/router';
import useToast from '../../stores/useToast';
import { t } from 'i18next';
import { MessageRegisterRequest } from '../../types/request';
import NumberOfRewards from '../sendMessageToIndividual/components/NumberOfRewards';
import ActionButtonBox from './components/ActionButtonBox';
import DateSettingBox from './components/DateSettingBox';
import { useQueryClient } from 'react-query';

const SendMessageToAll = () => {
	const navigate = useNavigate();

	const [inputValues, setInputValues] = useState<InputValueType>({
		subject: { value: '', error: '' },
		content: { value: '', error: '' },
	});
	const [numberOfRewards, setNumberOfRewards] = useState('1');
	const [selectedStartTime, setSelectedStartTime] = useState<TimeTableType>({ value: '00:00', label: '00:00' });
	const [selectedEndTime, setSelectedEndTime] = useState<TimeTableType>({ label: '00:00', value: '00:00' });
	const [selectedStartDate, setSelectedStartDate] = useState({
		startDate: null,
		endDate: null,
	});
	const [selectedEndDate, setSelectedEndDate] = useState({
		startDate: null,
		endDate: null,
	});

	const { handleInputsValue, handleInputsError } = useInput(setInputValues);
	const queryClient = useQueryClient();
	const messageAllRegister = useMessageAllRegisterMutation(
		() => {
			queryClient.invalidateQueries('messageAllList', {
				refetchInactive: true,
			});
			navigate(MESSAGES_ALL_URL);
			handleOpenToast();
		},
		() => {
			alert(t('알 수 없는 문제가 발생하였습니다.'));
		}
	);
	const { openToast } = useToast();

	const LG_TITLE_2 = 'text-xl font-bold leading-snug text-gray-800 mb-8';

	const handleStartDateChange = (newValue: DatePickerType) => {
		setSelectedStartDate(newValue);
	};

	const handleEndDateChange = (newValue: DatePickerType) => {
		setSelectedEndDate(newValue);
	};

	const handleOpenToast = () => {
		openToast({
			title: t('전체 우편 작성이 완료됐습니다.'),
			toastingTime: 5000,
			position: 'center',
			leftIcon: 'toast_success',
		});
	};

	const handleRegister = () => {
		const { subject, content } = inputValues;
		if (
			subject.value &&
			content.value &&
			numberOfRewards &&
			selectedStartDate.startDate &&
			selectedEndDate.endDate &&
			selectedStartTime &&
			selectedEndTime
		) {
			const body: MessageRegisterRequest = {
				title: subject.value,
				description: content.value,
				reward_type: 1,
				reward_id: 1,
				reward_count: Number(numberOfRewards.replaceAll(',', '')),
				start_time: selectedStartDate.startDate + 'T' + selectedStartTime.value + ':00',
				end_time: selectedEndDate.endDate + 'T' + selectedEndTime.value + ':00',
			};
			messageAllRegister.mutate(body);
		} else {
			handleInput();
		}
	};

	const handleInput = () => {
		if (!inputValues.title?.value) {
			handleInputsError('subject', t('공백을 포함하여 허용되는 최대 글자 수는 30자입니다.'));
		}
		if (!inputValues.description?.value) {
			handleInputsError('content', t('공백을 포함하여 권장 글자 수는 100자입니다.'));
		}
		if (!selectedStartDate.startDate || !selectedStartDate.endDate) {
			handleInputsError('startDate', t('날짜를 입력하세요.'));
		}
		if (!selectedEndDate.startDate || !selectedEndDate.endDate) {
			handleInputsError('endDate', t('날짜를 입력하세요.'));
		}
		if (!selectedStartTime.label || !selectedStartTime.value) {
			handleInputsError('startTime', t('시간을 입력하세요.'));
		}
		if (!selectedEndTime.label || !selectedEndTime.value) {
			handleInputsError('endTime', t('시간을 입력하세요.'));
		}
	};

	return (
		<Layout>
			<div className='h-[calc(100vh-128px)] overflow-scroll border-t border-t-gray-200'>
				<ActionButtonBox activeEventFunction={handleRegister} />
				<div className='flex justify-center w-full'>
					<section className='w-full max-w-xl p-10 pb-20'>
						<p className={LG_TITLE_2}>{t('정보')}</p>
						<div className='flex flex-col gap-8 mb-12'>
							<div className='flex flex-col gap-2'>
								<Title>{t('주제')}</Title>
								<Input
									placeholder={t('주제를 입력해주세요')}
									onChange={(value) => handleInputsValue('subject', value)}
									size='lg'
									value={inputValues.subject.value}
									error={inputValues.subject.error}
									onError={(error) => handleInputsError('subject', error)}
									helper={
										<p className='text-sm font-medium leading-5 text-gray-500'>
											{t('공백을 포함하여 권장되는 글자 수는 10자입니다.')}
										</p>
									}
								></Input>
							</div>
							<div className='flex flex-col gap-2'>
								<Title>{t('내용')}</Title>
								<Input
									placeholder={t('메일 내용을 입력해주세요')}
									onChange={(value) => handleInputsValue('content', value)}
									size='lg'
									value={inputValues.content.value}
									error={inputValues.content.error}
									onError={(error) => handleInputsError('content', error)}
									helper={
										<p className='text-sm font-medium leading-5 text-gray-500'>
											{t('공백을 포함하여 권장되는 글자 수는 25자입니다.')}
										</p>
									}
								></Input>
							</div>
						</div>
						<div>
							<p className={LG_TITLE_2}>{t('보상 설정')}</p>
							<NumberOfRewards numberOfRewards={numberOfRewards} setNumberOfRewards={setNumberOfRewards} />
						</div>
						<p className={LG_TITLE_2}>{t('날짜 설정')}</p>
						<DateSettingBox
							date={{
								startDate: {
									onChange: handleStartDateChange,
									value: selectedStartDate,
									error: inputValues.startDate?.error,
									onError: (error: string) => handleInputsError('startDate', error),
								},
								endDate: {
									onChange: handleEndDateChange,
									value: selectedEndDate,
									error: inputValues.endDate?.error,
									onError: (error: string) => handleInputsError('endDate', error),
								},
							}}
							time={{
								startTime: { selectedTime: selectedStartTime, setSelectedTime: setSelectedStartTime },
								endTime: { selectedTime: selectedEndTime, setSelectedTime: setSelectedEndTime },
							}}
						></DateSettingBox>
					</section>
				</div>
			</div>
		</Layout>
	);
};

export default SendMessageToAll;
