import { t } from 'i18next';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import DatePicker, { DatePickerType } from '../../components/DatePicker/DatePicker';
import Input from '../../components/Input/Input';
import Layout from '../../components/Layout/Layout';
import NoResult from '../../components/Modal/common/NoResult';
import SelectRecipient from '../../components/Modal/message/SelectRecipient';
import Radio from '../../components/Radio/Radio';
import { MESSAGES_INDIVIDUAL_URL } from '../../constants/router';
import { LG_TITLE_2 } from '../../constants/typo';
import useInput from '../../hooks/useInput';
import { useUserListQuery2 } from '../../queries/authQuery';
import { useMessageIndividualRegisterMutation } from '../../queries/messageQuery';
import useModal from '../../stores/useModal';
import useToast from '../../stores/useToast';
import { InputValueType } from '../../types/common';
import { IndividualMessageRegisterRequest } from '../../types/request';
import Title from '../sendMessageToAll/components/Title';
import ActionButtonBox from './components/ActionButtonBox';
import NumberOfRewards from './components/NumberOfRewards';
import Search from './components/Search';

const SendMessageToIndividual = () => {
	const navigate = useNavigate();

	const [selectedExpiredDate, setSelectedExpiredDate] = useState({
		startDate: null,
		endDate: null,
	});
	const [inputValues, setInputValues] = useState<InputValueType>({
		subject: { value: '', error: '' },
		content: { value: '', error: '' },
	});
	const [numberOfRewards, setNumberOfRewards] = useState('1');
	const [selectedType, setSelectedType] = useState('loginId');
	const [keyword, setKeyword] = useState('');
	const [selectedUser, setSelectedUser] = useState<{ id: string; name: string }>({ id: '', name: '' });

	const { openToast } = useToast();
	const { openModal, closeModal } = useModal();
	const userList = useUserListQuery2(selectedType, keyword, (response) => {
		const existList = Boolean(response.list.length);
		openModal({
			title: existList ? t('수신자 선택') : t('결과 없음'),
			content: existList ? (
				<SelectRecipient
					list={response.list}
					selectedUser={selectedUser}
					setSelectedUser={setSelectedUser}
					onConfirm={(value) => {
						setSelectedUser(value);
						closeModal();
					}}
				/>
			) : (
				<NoResult />
			),
			size: existList ? 'lg' : 'sm',
		});
	});
	const messageAllRegister = useMessageIndividualRegisterMutation(
		() => {
			navigate(MESSAGES_INDIVIDUAL_URL);
			handleOpenToast();
		},
		() => {
			alert(t('알 수 없는 문제가 발생하였습니다.'));
		}
	);
	const { handleInputsValue, handleInputsError } = useInput(setInputValues);

	const handleExpiredDateChange = (newValue: DatePickerType) => {
		setSelectedExpiredDate(newValue);
	};

	const onSearch = () => {
		if (!keyword)
			openModal({
				title: 'No Results',
				content: <NoResult />,
				size: 'sm',
			});
		else userList.refetch();
	};

	const onReset = () => {
		setKeyword('');
		setSelectedType('learnerName');
		setSelectedUser({ id: '', name: '' });
	};

	const handleOpenToast = () => {
		openToast({
			title: '개인 우편 작성이 완료됐습니다.',
			toastingTime: 5000,
			position: 'center',
			leftIcon: 'toast_success',
		});
	};

	const handleRegister = () => {
		const { subject, content } = inputValues;
		if (selectedUser.id && subject.value && content.value && numberOfRewards && selectedExpiredDate) {
			const body: IndividualMessageRegisterRequest = {
				user_id: Number(selectedUser.id),
				title: subject.value,
				description: content.value,
				reward_type: 1,
				reward_id: 1,
				reward_count: Number(numberOfRewards.replaceAll(',', '')),
				expiry_time: selectedExpiredDate.startDate + 'T23:59:59',
			};
			messageAllRegister.mutate(body);
		} else {
			handleInput();
		}
	};

	const handleInput = () => {
		if (!inputValues.user?.value) {
			handleInputsError('user', 'Please enter a recipient.');
		}
		if (!inputValues.subject?.value) {
			handleInputsError('subject', 'The maximum number of characters allowed, including spaces, is 30.');
		}
		if (!inputValues.content?.value) {
			handleInputsError('content', 'The number of characters recommended, including spaces, is 100.');
		}
		if (!selectedExpiredDate.startDate || !selectedExpiredDate.endDate) {
			handleInputsError('expiredDate', 'Please enter the date.');
		}
	};

	return (
		<Layout>
			<div className='h-[calc(100vh-128px)] flex w-full justify-center overflow-scroll'>
				<ActionButtonBox activeEventFunction={handleRegister} />
				<section className='w-full max-w-xl p-10 pb-20 h-fit'>
					<p className={LG_TITLE_2}>{t('정보')}</p>
					<div className='flex flex-col gap-8 mb-12'>
						<div className='flex flex-col gap-4'>
							<Title>{t('수령인')}</Title>
							<div className='flex flex-col gap-5'>
								<div className='flex gap-4'>
									<Radio
										id='loginId'
										selected={selectedType}
										label={t('유저 아이디')}
										onChange={(value) => typeof value === 'string' && setSelectedType(value)}
									/>
									<Radio
										id='learnerName'
										selected={selectedType}
										label={t('학생 이름')}
										onChange={(value) => typeof value === 'string' && setSelectedType(value)}
									/>
								</div>
								<Search
									error={inputValues.user?.error}
									onError={(error) => handleInputsError('user', error)}
									keyword={keyword}
									onChange={(value: string) => setKeyword(value)}
									onSearch={onSearch}
									onReset={onReset}
									placeholder='키워드 입력'
								/>
							</div>
						</div>
						<div className='flex flex-col gap-2'>
							<Title>{t('주제')}</Title>
							<Input
								placeholder={t('주제를 입력해주세요')}
								onChange={(value) => handleInputsValue('subject', value)}
								size='lg'
								value={inputValues.subject.value}
								error={inputValues.subject.error}
								onError={(error) => handleInputsError('subject', error)}
								helper={
									<p className='text-sm font-medium leading-5 text-gray-500'>
										{t('공백을 포함하여 권장되는 글자 수는 10자입니다.')}
									</p>
								}
							></Input>
						</div>
						<div className='flex flex-col gap-2'>
							<Title>{t('내용')}</Title>
							<Input
								placeholder={t('메일 내용을 입력해주세요')}
								onChange={(value) => handleInputsValue('content', value)}
								size='lg'
								value={inputValues.content.value}
								error={inputValues.content.error}
								onError={(error) => handleInputsError('content', error)}
								helper={
									<p className='text-sm font-medium leading-5 text-gray-500'>
										{t('공백을 포함하여 권장되는 글자 수는 25자입니다.')}
									</p>
								}
							></Input>
						</div>
					</div>
					<div>
						<p className={LG_TITLE_2}>{t('보상 설정')}</p>
						<NumberOfRewards numberOfRewards={numberOfRewards} setNumberOfRewards={setNumberOfRewards} />
					</div>
					<p className={LG_TITLE_2}>{t('날짜 설정')}</p>
					<div>
						<div className='relative flex flex-col gap-2 w-[256px]'>
							<Title>{t('만료일')}</Title>
							<DatePicker
								error={inputValues.expiredDate?.error}
								onError={(error) => handleInputsError('expiredDate', error)}
								asSingle={true}
								useRange={false}
								placeholder='Choose the date'
								onChange={handleExpiredDateChange}
								value={selectedExpiredDate}
							></DatePicker>
						</div>
					</div>
				</section>
			</div>
		</Layout>
	);
};

export default SendMessageToIndividual;
