import { useCallback, useEffect, useState } from 'react';
import { t } from 'i18next';
import { useQueryClient } from 'react-query';
import { useNavigate } from 'react-router-dom';
import ImagePreview from '../../components/ImagePreview/ImagePreview';
import Layout from '../../components/Layout/Layout';
import DiscardNotice from '../../components/Modal/notice/DiscardNotice';
import { NOTICE_URL } from '../../constants/router';
import useNoticeInput from '../../hooks/useNoticeInput';
import { useNoticeRegisterMutation } from '../../queries/noticeQuery';
import useModal from '../../stores/useModal';
import useToast from '../../stores/useToast';
import { NoticeInputValuesType } from '../../types/notice';
import NoticeRegisterInfo from './components/NoticeRegisterInfo';
import SubHeader from './components/SubHeader';
import RowTable, { ColumnType, DataType } from '../../components/RowTable/RowTable';
import { ChevronUpDownIcon } from '@heroicons/react/24/outline';

const NoticeRegistration = () => {
	const navigate = useNavigate();
	const { openToast } = useToast();
	const { openModal, closeModal } = useModal();

	const [inputValues, setInputValues] = useState<NoticeInputValuesType>({
		title: { value: '', error: '' },
		description: { value: '', error: '' },
		imageUploadName: { value: '', error: '' },
		imageS3Url: { value: '', error: '' },
		imageSrc: { value: '', error: '' },
		urlLink: { value: '', error: '' },
		date: {
			value: {
				startDate: null,
				endDate: null,
			},
			error: '',
		},
	});

	const { handleNoticeInputValue, handleNoticeInputError } = useNoticeInput(inputValues, setInputValues);
	const queryClient = useQueryClient();

	const noticeRegister = useNoticeRegisterMutation(
		() => {
			queryClient.invalidateQueries('noticeList', {
				refetchInactive: true,
			});
			navigate(NOTICE_URL);
			handleOpenToast();
		},
		() => {
			alert(t('알 수 없는 문제가 발생하였습니다.'));
		}
	);

	const handleOpenToast = () => {
		openToast({
			title: t('공지사항 등록이 완료되었습니다.'),
			toastingTime: 5000,
			position: 'center',
			leftIcon: 'toast_success',
		});
	};

	const noticeDelete = () => {
		closeModal();
		navigate(NOTICE_URL);
	};

	const handleCancelModal = () => {
		openModal({
			title: t('등록 취소'),
			subTitle: (
				<>
					<p>{t('공지사항 등록을 취소하시겠습니까?')}</p>
					<p>{t('해당 공지사항은 삭제되고 저장되지 않습니다.')}</p>
				</>
			),
			content: <DiscardNotice onConfirm={() => noticeDelete()} />,
			size: 'sm',
		});
	};

	const handleRegister = () => {
		if (
			inputValues.title.value &&
			inputValues.description.value &&
			inputValues.imageUploadName.value &&
			inputValues.date.value &&
			inputValues.imageS3Url.value
		) {
			const body = {
				title: inputValues.title.value,
				body: inputValues.description.value,
				link: inputValues.urlLink.value,
				start_time: inputValues.date.value.startDate + 'T00:00:00.945Z',
				end_time: inputValues.date.value.endDate + 'T23:59:59.945Z',
				img_url: inputValues.imageS3Url.value,
			};
			noticeRegister.mutate(body);
		} else {
			handleInput();
		}
	};

	const handleInput = () => {
		if (!inputValues.title.value) {
			handleNoticeInputError('title', t('제목을 입력해주세요.'));
		}
		if (!inputValues.description.value) {
			handleNoticeInputError('description', t('본문을 입력해주세요.'));
		}
		if (!inputValues.imageUploadName.value) {
			handleNoticeInputError('imageUploadName', t('이미지를 업로드해주세요.'));
		}
		if (!inputValues.date.value.startDate || !inputValues.date.value.endDate) {
			handleNoticeInputError('date', t('날짜를 입력해주세요.'));
		}
	};

	const [columns, setColumns] = useState<ColumnType<DataType>[]>();
	const [data, setData] = useState<DataType[]>();
	const handleNameIcon = useCallback(() => {
		console.log(data);
		if (!data || !data.length) {
			// data가 undefined 또는 빈 배열일 때의 처리
			return;
		}
		const sortedData = [...data].sort((a, b) => {
			const nameA = String(a['name']).toLowerCase();
			const nameB = String(b['name']).toLowerCase();
			return nameA.localeCompare(nameB);
		});

		console.log(sortedData);
		setData(sortedData);
	}, [data]);
	const tmp: ColumnType<DataType>[] = [
		{
			title: '학부모 계정',
			dataIndex: 'name',
			onClickIcon: handleNameIcon,
		},
		{
			title: '학습자 계정',
			dataIndex: 'age',
		},
		{
			title: '결제 정보',
			dataIndex: 'address',
		},
		{
			title: '피드백',
			dataIndex: 'feedback',
		},
	];

	const tmp2: DataType[] = [
		{
			id: '1',
			name: 'John Brown',
			age: 32,
			address: 'New York No. 1 Lake Park',
			feedback: 'good girl',
		},
		{
			id: '2',
			name: 'Jim Green',
			age: 42,
			address: 'London No. 1 Lake Park',
			feedback: 'good girl',
		},
		{
			id: '3',
			name: 'Joe Black',
			age: 32,
			address: 'Sydney No. 1 Lake Park',
			feedback: 'good girl',
		},
		{
			id: '4',
			name: 'Disabled User',
			age: 99,
			address: 'Sydney No. 1 Lake Park',
			feedback: 'good girl',
		},
		{
			id: '5',
			name: '소희',
			age: 9,
			address: 'Sydney No. 1 Lake Park',
			feedback: 'good girl',
		},
	];

	useEffect(() => {
		setColumns([...tmp]);
		setData([...tmp2]);
	}, []);

	return (
		<Layout>
			<SubHeader handleRegister={handleRegister} handleCancel={handleCancelModal} />
			{/* <div className='flex grow w-full h-[calc(100vh_-_187px)]'>
				<NoticeRegisterInfo
					inputValues={inputValues}
					handleNoticeInputValue={handleNoticeInputValue}
					handleNoticeInputError={handleNoticeInputError}
					handleInput={handleInput}
				/>
				<div className='px-10 pt-10 pb-20 overflow-auto scroll-none'>
					<p className='pb-8 text-xl font-bold text-gray-800'>{t('공지사항 미리보기')}</p>
					<ImagePreview
						title={inputValues?.title.value}
						body={inputValues?.description.value}
						imageUrl={inputValues.imageSrc.value}
					/>
				</div>
			</div> */}
			{data && columns && (
				<RowTable columns={columns} data={data} isCheckBox={true} rightIcon={<ChevronUpDownIcon />} />
			)}
		</Layout>
	);
};

export default NoticeRegistration;
