import { t } from 'i18next';
import { memo } from 'react';
import Badge from '../../../../components/Badge/Badge';
import { NoticeListType } from '../../../../types/notice';
import { conversionDate } from '../../../../utils/moment';
import { cn } from '../../../../utils/style';
import { ListItemStyle } from './style';

interface ItemProps {
	item: NoticeListType;
	activeNotice: { id: string; index: number };
	onClick: (id: string, index: number) => void;
}

const ListItem = ({ item, activeNotice, onClick }: ItemProps) => {
	return (
		<li
			className={cn(ListItemStyle({ active: item.notice_id === activeNotice.id }))}
			onClick={() => onClick(item.notice_id, item.index)}
		>
			<div className='text-base text-gray-900'>{item.index}</div>
			<div className='grow'>
				<p className='mb-2 text-base text-gray-900 break-all text-ellipsis-1'>{item.title}</p>
				<p className='mb-1 text-sm text-gray-500'>
					{t('노출 날짜')} {conversionDate(item.start_time, 'YYYY-MM-DD')}
					{conversionDate(item.end_time, ' - YYYY-MM-DD')}
				</p>
				<p className='text-sm text-gray-500'>
					{t('등록 날짜')} {item.registered}
				</p>
			</div>
			{item.exposure && <Badge color='blue' size='sm' label={t('게시')} className='self-start ml-auto' />}
		</li>
	);
};

export default memo(
	ListItem,
	(prev: ItemProps, next: ItemProps) =>
		prev.item === next.item && prev.activeNotice === next.activeNotice && prev.onClick === next.onClick
);
