import React from 'react';

interface TitleProps {
	children: string;
	className?: string;
	isStar?: boolean;
}

const Title = ({ children, className, isStar }: TitleProps) => {
	return (
		<div className={`${className ? className : ''} flex items-center text-sm`}>
			{!isStar && <span className='mr-1 text-red-600'>*</span>}
			<span>{children}</span>
		</div>
	);
};

export default Title;
