import { t } from 'i18next';
import useToast from '../stores/useToast';

const useNoticeToast = () => {
	const { openToast, closeToast } = useToast();

	const handleOpenSizeToast = () => {
		openToast({
			title: 'Image Upload Failed',
			description: (
				<>
					<p>{t('이미지 최대 크기가 500KB를 초과하였습니다.')}</p>
					<p>{t('500KB 미만 크기의 이미지로 업로드해주세요.')}</p>
				</>
			),
			rightButton: { text: t('확인'), onClick: () => closeToast() },
			isClose: true,
			leftIcon: 'toast_warning',
			position: 'center',
		});
	};

	const handleOpenDimensionToast = () => {
		openToast({
			title: 'Image Upload Failed',
			description: (
				<>
					<p>{t('이미지 파일 크기는 800x250이어야 합니다.')}</p>
					<p>{t('적합한 크기의 이미지를 업로드해주세요.')}</p>
				</>
			),
			rightButton: { text: t('확인'), onClick: () => closeToast() },
			isClose: true,
			leftIcon: 'toast_warning',
			position: 'center',
		});
	};

	const handleOpenFormatToast = () => {
		openToast({
			title: 'Image Upload Failed',
			description: (
				<>
					<p>{t('이미지 파일 포맷이 적절하지 않습니다.')}</p>
					<p>{t('적합한 포맷의 이미지를 업로드해주세요.')}</p>
				</>
			),
			rightButton: { text: t('확인'), onClick: () => closeToast() },
			isClose: true,
			leftIcon: 'toast_warning',
			position: 'center',
		});
	};

	return { handleOpenSizeToast, handleOpenDimensionToast, handleOpenFormatToast };
};

export default useNoticeToast;
