import { t } from 'i18next';
import Radio from '../../../components/Radio/Radio';
import Search from '../../../components/Search/Search';

interface SearchBarProps {
	keyword: string;
	type: string;
	onSelect: (value: string) => void;
	onChange: (value: string) => void;
	onReset: () => void;
	onSearch: () => void;
}

const SearchBar = ({ keyword, type, onSelect, onChange, onReset, onSearch }: SearchBarProps) => {
	return (
		<div className='px-10 pb-6 border-b-[1px] border-gray-200 flex gap-4 items-center'>
			<div className='flex gap-4'>
				<Radio id='learnerName' selected={type} label={t('학생 이름')} onChange={onSelect} />
				<Radio id='loginId' selected={type} label={t('유저 아이디')} onChange={onSelect} />
			</div>
			<Search
				keyword={keyword}
				onChange={onChange}
				onSearch={onSearch}
				onReset={onReset}
				placeholder='학습자 이름 또는 사용자 ID 입력'
			/>
		</div>
	);
};

export default SearchBar;
