import { memo } from 'react';
import { LANGUAGE_LIST } from '../../constants/language';
import useLanguage from '../../stores/useLanguage';
import { LanguageType } from '../../types/language';
import Select from '../Select/Select';
import Title from '../Title/Title';

interface HeaderProps {
	title?: boolean;
	lang?: boolean;
}

const Header = ({ lang, title }: HeaderProps) => {
	const { language, setLanguage } = useLanguage();

	const handleChangeLanguage = (selectedLanguage: LanguageType) => {
		setLanguage(selectedLanguage);
	};

	return (
		<header className='relative w-full px-8 py-3'>
			{title && <Title />}
			{lang && (
				<div className='absolute w-20 top-3 right-8'>
					<Select
						size='md'
						selected={language}
						center={true}
						items={[{ value: 'en', label: 'EN' }, ...LANGUAGE_LIST]}
						onChange={(value) => handleChangeLanguage(value as LanguageType)}
					/>
				</div>
			)}
		</header>
	);
};

export default memo(
	Header,
	(prev: HeaderProps, next: HeaderProps) => prev.lang === next.lang && prev.title === next.title
);
