import { Dispatch, SetStateAction, memo } from 'react';
import CountText from '../../../components/CountText/CountText';
import { UserListType } from '../../../types/auth';
import ListItem from './ListItem/ListItem';

interface LearnerListProps {
	filterList: UserListType[];
	activeUser: number;
	setActiveUser: Dispatch<SetStateAction<number>>;
}

const LearnerList = ({ filterList, activeUser, setActiveUser }: LearnerListProps) => {
	const handleActiveUser = (id: number) => {
		setActiveUser(id);
	};

	return (
		<div className='w-[calc(100%_-_1025px)] h-full px-10 py-10 border-r-[1px] border-gray-200 overflow-auto scroll-none'>
			{!!filterList.length && <CountText list={filterList} className='mb-4' />}
			<ul>
				{filterList.map((item) => (
					<ListItem key={item.user_id} item={item} activeUser={activeUser} onClick={handleActiveUser} />
				))}
			</ul>
		</div>
	);
};

export default memo(
	LearnerList,
	(prev: LearnerListProps, next: LearnerListProps) =>
		prev.filterList === next.filterList &&
		prev.activeUser === next.activeUser &&
		prev.setActiveUser === next.setActiveUser
);
