/* eslint-disable @typescript-eslint/no-explicit-any */
import axios, { AxiosResponse } from 'axios';
import useAuth from '../stores/useAuth';
import { RefreshResponse } from '../types/response';

const { setAccessToken, setRefreshToken } = useAuth.getState();

const axiosInstance = axios.create({
	baseURL: process.env.REACT_APP_API_URL,
	timeout: 60000,
	headers: {
		'Content-Type': 'application/json',
	},
	withCredentials: true,
});

const updateAccessToken = async () => {
	const { data } = await axiosInstance.get<RefreshResponse>('/api/v1/token');

	if (data.data.access_token) {
		setAccessToken(data.data.access_token);
		return data.data.access_token;
	}
};

axiosInstance.interceptors.request.use(async (request) => {
	const accessToken = useAuth.getState().accessToken;
	const refreshToken = useAuth.getState().refreshToken;

	if (accessToken) {
		request.headers.Authorization = `Bearer ${request.url === '/api/v1/token' ? refreshToken : accessToken}`;
	}

	return request;
});

axiosInstance.interceptors.response.use(
	(response: AxiosResponse) => {
		if (response.config.url === '/api/v1/login') {
			const { access_token, refresh_token } = response.data.data;
			setAccessToken(access_token);
			setRefreshToken(refresh_token);
		}

		return response;
	},
	async (error: any) => {
		if (error.response.data.code === -2201) {
			const newAccessToken = await updateAccessToken();

			error.config.headers.Authorization = `Bearer ${newAccessToken}`;

			return await axios.request(error.config);
		}

		return error.response;
	}
);

export default axiosInstance;
