import { t } from 'i18next';
import { UserListType } from '../../types/auth';
import { MessageAllListType, MessageIndividualListType } from '../../types/messages';
import { NoticeListType } from '../../types/notice';
import { memo } from 'react';

interface CountTextProps {
	list: NoticeListType[] | MessageAllListType[] | MessageIndividualListType[] | UserListType[];
	className?: string;
}

const CountText = ({ list, className }: CountTextProps) => {
	return (
		<p className={`text-base font-medium ${className}`}>
			<span className='text-blue-600'>
				{list?.length} {list?.length > 1 ? t('개의 결과가.복수') : t('개의 결과가.단수')}
			</span>{' '}
			<span className='text-gray-700'>{t('검색되었습니다.')}</span>
		</p>
	);
};

export default memo(
	CountText,
	(prev: CountTextProps, next: CountTextProps) => prev.list === next.list && prev.className === next.className
);
