import { Dispatch, SetStateAction, memo } from 'react';
import CountText from '../../../components/CountText/CountText';
import { NoticeListType } from '../../../types/notice';
import ListItem from './ListItem/ListItem';

interface NoticeListProps {
	noticeList: NoticeListType[];
	filterList: NoticeListType[];
	activeNotice: { id: string; index: number };
	setActiveNotice: Dispatch<SetStateAction<{ id: string; index: number }>>;
}

const NoticeList = ({ noticeList, filterList, activeNotice, setActiveNotice }: NoticeListProps) => {
	const handleActiveNotice = (id: string, index: number) => {
		setActiveNotice({ id: id, index: index });
	};

	return (
		<div className='w-[calc(100%_-_750px)] h-full px-10 py-10 border-r-[1px] border-gray-200 overflow-auto scroll-none'>
			{filterList?.length !== noticeList?.length && !!filterList?.length && (
				<CountText list={filterList} className='mb-4' />
			)}
			<ul>
				{filterList?.map((item) => (
					<ListItem key={item.notice_id} item={item} activeNotice={activeNotice} onClick={handleActiveNotice} />
				))}
			</ul>
		</div>
	);
};

export default memo(
	NoticeList,
	(prev: NoticeListProps, next: NoticeListProps) =>
		prev.noticeList === next.noticeList &&
		prev.filterList === next.filterList &&
		prev.activeNotice === next.activeNotice &&
		prev.setActiveNotice === next.setActiveNotice
);
