import { useEffect, useLayoutEffect, useState } from 'react';
import { t } from 'i18next';
import { useQueryClient } from 'react-query';
import { useLocation, useNavigate } from 'react-router-dom';
import { DateType } from 'react-tailwindcss-datepicker';
import ImagePreview from '../../components/ImagePreview/ImagePreview';
import Layout from '../../components/Layout/Layout';
import CancelModify from '../../components/Modal/notice/CancelModify';
import DiscardNotice from '../../components/Modal/notice/DiscardNotice';
import { NOTICE_URL } from '../../constants/router';
import useNoticeInput from '../../hooks/useNoticeInput';
import { useNoticeDetailQuery, useNoticeEditMutation } from '../../queries/noticeQuery';
import useModal from '../../stores/useModal';
import useToast from '../../stores/useToast';
import { NoticeInputValuesType } from '../../types/notice';
import NoticeEditInfo from './components/NoticeEditInfo';
import SubHeader from './components/SubHeader';

const NoticeEdit = () => {
	const navigate = useNavigate();
	const location = useLocation();
	const queryClient = useQueryClient();

	const [inputValues, setInputValues] = useState<NoticeInputValuesType>({
		title: { value: '', error: '' },
		description: { value: '', error: '' },
		imageUploadName: { value: '', error: '' },
		imageS3Url: { value: '', error: '' },
		imageSrc: { value: '', error: '' },
		urlLink: { value: '', error: '' },
		date: {
			value: {
				startDate: null,
				endDate: null,
			},
			error: '',
		},
	});

	const [isNotCancel, setIsNotCancel] = useState<boolean>(false);
	const [body, setBody] = useState<{
		title: string;
		body: string;
		link: string;
		start_time: DateType;
		end_time: DateType;
		img_url: string;
	} | null>();

	const { openToast } = useToast();
	const { openModal, closeModal } = useModal();
	const { handleNoticeInputValue, handleNoticeInputError } = useNoticeInput(inputValues, setInputValues);
	const id = location.state;

	const noticeDetail = useNoticeDetailQuery(id);
	const item = noticeDetail.item;

	const noticeEdit = useNoticeEditMutation(
		id,
		() => {
			queryClient.invalidateQueries('noticeList', {
				refetchInactive: true,
			});
			navigate(NOTICE_URL);
			handleOpenToast();
		},
		(error: number) => {
			if (error === -2901) alert(t('존재하지 않는 공지사항입니다.'));
			else alert(t('알 수 없는 문제가 발생하였습니다.'));
		}
	);

	const handleOpenToast = () => {
		openToast({
			title: 'The notice has been changed.',
			toastingTime: 5000,
			position: 'center',
			leftIcon: 'toast_success',
		});
	};

	const noticeDelete = () => {
		closeModal();
		navigate(NOTICE_URL);
	};

	const cancelModify = () => {
		closeModal();
		setIsNotCancel(false);
	};

	const continueModify = () => {
		closeModal();
		setIsNotCancel(true);
	};

	const handleCancelModal = () => {
		openModal({
			title: t('수정 취소'),
			subTitle: (
				<>
					<p>{t('공지사항 수정을 취소하시겠습니까?')}</p>
				</>
			),
			content: <DiscardNotice onConfirm={() => noticeDelete()} />,
			size: 'sm',
		});
	};

	const handleOutOfDateModal = () => {
		openModal({
			title: t('날짜 변경'),
			subTitle: (
				<>
					<p>{t('수정하신대로 노출 날짜 범위를 변경하시게 되면, 공지사항이 게임에서 제거됩니다.')}</p>
				</>
			),
			content: <CancelModify onCancel={() => cancelModify()} onConfirm={() => continueModify()} />,
			size: 'sm',
		});
	};

	const handleContainDateModal = () => {
		openModal({
			title: t('날짜 변경'),
			subTitle: (
				<>
					<p>{t('수정하신대로 노출 날짜 범위를 변경하시게 되면, 공지사항이 게임에 게시됩니다.')}</p>
				</>
			),
			content: <CancelModify onCancel={() => cancelModify()} onConfirm={() => continueModify()} />,
			size: 'sm',
		});
	};

	const checkDate = (prev: DateType, end: DateType) => {
		const today = new Date();
		const startDate = new Date(prev);
		const endDate = new Date(end);

		return startDate <= today && today <= endDate;
	};

	const handleRegister = () => {
		if (
			inputValues.title.value &&
			inputValues.description.value &&
			inputValues.imageUploadName.value &&
			inputValues.date.value &&
			inputValues.imageS3Url.value
		) {
			const res = checkDate(inputValues.date.value.startDate, inputValues.date.value.endDate);

			if (res) {
				handleContainDateModal();
			} else {
				handleOutOfDateModal();
			}

			setBody({
				title: inputValues.title.value,
				body: inputValues.description.value,
				link: inputValues.urlLink.value,
				start_time: inputValues.date.value.startDate,
				end_time: inputValues.date.value.endDate,
				img_url: inputValues.imageS3Url.value,
			});
		} else {
			handleInput();
		}
	};

	const handleInput = () => {
		if (!inputValues.title.value) {
			handleNoticeInputError('title', t('제목을 입력해주세요.'));
		}
		if (!inputValues.description.value) {
			handleNoticeInputError('description', t('본문을 입력해주세요.'));
		}
		if (!inputValues.imageUploadName.value) {
			handleNoticeInputError('imageUploadName', t('이미지를 업로드해주세요.'));
		}
		if (!inputValues.date.value.startDate || !inputValues.date.value.endDate) {
			handleNoticeInputError('date', t('날짜를 입력해주세요.'));
		}
	};

	useEffect(() => {
		if (isNotCancel && body) {
			noticeEdit.mutate({ id, body });
		}
	}, [isNotCancel, body]);

	useLayoutEffect(() => {
		if (!item) return;

		setIsNotCancel(false);
		handleNoticeInputValue('title', item.title);
		handleNoticeInputValue('description', item.body);
		handleNoticeInputValue('imageUploadName', item.img_url);
		handleNoticeInputValue('urlLink', item.link);
		handleNoticeInputValue('date', { startDate: item.start_time, endDate: item.end_time });
		handleNoticeInputValue('imageS3Url', item.img_url);
		handleNoticeInputValue('imageSrc', item.img_url);
	}, [item]);

	return (
		<Layout>
			<SubHeader handleRegister={handleRegister} handleCancel={handleCancelModal} />
			<div className='flex grow w-full h-[calc(100vh_-_187px)]'>
				<NoticeEditInfo
					inputValues={inputValues}
					handleNoticeInputValue={handleNoticeInputValue}
					handleNoticeInputError={handleNoticeInputError}
					handleInput={handleInput}
				/>
				<div className='px-10 pt-10 pb-20 overflow-auto scroll-none'>
					<p className='pb-8 text-xl font-bold text-gray-800'>{t('공지사항 미리보기')}</p>
					<ImagePreview
						title={inputValues?.title.value}
						body={inputValues?.description.value}
						imageUrl={inputValues.imageSrc.value}
					/>
				</div>
			</div>
		</Layout>
	);
};

export default NoticeEdit;
