import { memo, useState } from 'react';
import CheckBox from '../CheckBox/CheckBox';

export interface ColumnType<RecordType> {
	title: React.ReactNode;
	dataIndex: keyof RecordType;
	onClickIcon?: () => void;
	id?: React.Key;
}

export interface DataType {
	id: React.Key;
	[key: string]: string | number;
}

interface RowTableProps {
	columns: ColumnType<DataType>[];
	data: DataType[];
	isCheckBox: boolean;
	rightIcon?: string | JSX.Element;
}

const rowSelection = {
	onChange: (selectedRowKeys: React.Key[], selectedRows: DataType[]) => {
		console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
	},
	getCheckboxProps: (record: DataType) => ({
		disabled: record.name === 'Disabled User', // Column configuration not to be checked
		name: record.name,
	}),
};

function RowTable({ columns, data, isCheckBox, rightIcon }: RowTableProps) {
	console.log(columns, data);
	const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);

	console.log(columns[0].onClickIcon);

	return (
		<div className='flex pb-0 flex-col items-start  w-full'>
			{/* 결과 div */}
			<div className='flex pb-4 items-start '>
				<span className='text-gray-800 font-medium text-base leading-6.5'>
					<span className='text-blue-600'>5</span>개의 결과가 검색되었어요.
				</span>
			</div>
			<table className='w-full'>
				<thead className='flex bg-gray-50 border-b border-gray-200 text-gray-500 font-semibold leading-5 text-sm h-[44px]'>
					<tr className='flex items-center w-full'>
						{isCheckBox && (
							<th className='px-4 py-3 font-semibold min-w-fit '>
								<CheckBox
									checked={selectedRowKeys.length === data?.length}
									onChange={() => {
										const allSelected = selectedRowKeys.length === data?.length;
										if (allSelected) {
											setSelectedRowKeys([]);
										} else {
											const allIds = data?.map((record) => record.id);

											setSelectedRowKeys(allIds);
										}
									}}
								/>
							</th>
						)}
						{columns?.map((column) => (
							<th key={column.id} className='w-full flex justify-start px-4 py-3 space-x-2 font-semibold'>
								<span>{column.title}</span>
								{rightIcon && (
									<div className='w-5' onClick={column.onClickIcon}>
										{rightIcon}
									</div>
								)}
							</th>
						))}
					</tr>
				</thead>
				<tbody>
					{data?.map((record) => (
						<tr key={record.id} className='flex border-b border-solid items-center border-b-gray-200'>
							{isCheckBox && (
								<td className=' flex py-3 px-4 '>
									<CheckBox
										checked={selectedRowKeys.includes(record.id)}
										// label='라벨 텍스트'
										// helper='헬퍼 텍스트'
										onChange={() => {
											const selectedId = record.id;
											const newSelectedKeys = selectedRowKeys.includes(selectedId)
												? selectedRowKeys.filter((key) => key !== selectedId)
												: [...selectedRowKeys, selectedId];
											setSelectedRowKeys(newSelectedKeys);
										}}
									/>
								</td>
							)}

							{columns?.map((column) => (
								<td key={column.id} className='w-full flex justify-start px-4 py-3 font-medium text-sm leading-5 '>
									{record[column.dataIndex]}
								</td>
							))}
						</tr>
					))}
				</tbody>
			</table>
		</div>
	);
}

export default memo(
	RowTable,
	(prev: RowTableProps, next: RowTableProps) =>
		prev.columns === next.columns &&
		prev.data === next.data &&
		prev.isCheckBox === next.isCheckBox &&
		prev.rightIcon === next.rightIcon
);
