import { Dispatch, SetStateAction } from 'react';
import { NoticeInputValuesType } from '../types/notice';

const useNoticeInput = (
	inputValues: NoticeInputValuesType,
	setInputValues: Dispatch<SetStateAction<NoticeInputValuesType>>
) => {
	const handleNoticeInputValue = (
		type: keyof NoticeInputValuesType,
		value: NoticeInputValuesType[keyof NoticeInputValuesType]['value']
	) => {
		setInputValues((state) => ({ ...state, [type]: { ...state[type], value: value } }));
	};

	const handleNoticeInputError = (type: keyof NoticeInputValuesType, error: string) => {
		setInputValues((state) => ({ ...state, [type]: { ...state[type], error: error } }));
	};

	return { handleNoticeInputValue, handleNoticeInputError };
};

export default useNoticeInput;
