import { t } from 'i18next';
import useModal from '../../../stores/useModal';
import Button from '../../Button/Button';

interface UndoPostingProps {
	onUndo: () => void;
}

const UndoPosting = ({ onUndo: onConfirm }: UndoPostingProps) => {
	const { closeModal } = useModal();

	return (
		<div className='flex gap-3'>
			<Button color='gray' size='lg' isFull={true} label={t('취소')} onClick={closeModal} />
			<Button color='red' size='lg' isFull={true} label={t('게시 취소')} onClick={onConfirm} />
		</div>
	);
};

export default UndoPosting;
