import { ReactElement, memo } from 'react';

interface DetailItemProps {
	title: string | ReactElement;
	content: number | string;
}

const DetailItem = ({ title, content }: DetailItemProps) => {
	return (
		<div className='flex justify-between border-gray-200 border-b-[1px]'>
			<span className='w-[55%] py-3 pr-3 text-gray-500 text-sm font-medium'>{title}</span>
			<span className='w-[45%] overflow-hidden p-3 text-slate-900 text-sm font-medium'>{content}</span>
		</div>
	);
};

export default memo(
	DetailItem,
	(prev: DetailItemProps, next: DetailItemProps) => prev.title === next.title && prev.content === next.content
);
