import { Navigate, Route, Routes } from 'react-router-dom';
import {
	LEARNER_INFO_URL,
	LOGIN_URL,
	MESSAGES_ALL_URL,
	MESSAGES_INDIVIDUAL_URL,
	MESSAGE_SEND_TO_ALL_URL,
	MESSAGE_SEND_TO_INDIVIDUAL_URL,
	NOTICE_EDIT_URL,
	NOTICE_REGISTRATION_URL,
	NOTICE_URL,
} from '../constants/router';
import LearnerInfo from '../pages/learnerInfo';
import Login from '../pages/login';
import MessageAll from '../pages/messageAll';
import MessageIndividual from '../pages/messageIndividual';
import Notice from '../pages/notice';
import NoticeEdit from '../pages/noticeEdit';
import NoticeRegistration from '../pages/noticeRegistration';
import SendMessageToAll from '../pages/sendMessageToAll';
import SendMessageToIndividual from '../pages/sendMessageToIndividual';
import ProtectedRoute from './ProtectedRoute';

const Router = () => (
	<Routes>
		<Route
			path={LOGIN_URL}
			element={
				<ProtectedRoute pathname={LOGIN_URL}>
					<Login />
				</ProtectedRoute>
			}
		/>
		<Route
			path={NOTICE_URL}
			element={
				<ProtectedRoute pathname={NOTICE_URL}>
					<Notice />
				</ProtectedRoute>
			}
		/>
		<Route
			path={NOTICE_EDIT_URL}
			element={
				<ProtectedRoute pathname={NOTICE_EDIT_URL}>
					<NoticeEdit />
				</ProtectedRoute>
			}
		/>
		<Route
			path={NOTICE_REGISTRATION_URL}
			element={
				<ProtectedRoute pathname={NOTICE_REGISTRATION_URL}>
					<NoticeRegistration />
				</ProtectedRoute>
			}
		/>
		<Route
			path={MESSAGES_ALL_URL}
			element={
				<ProtectedRoute pathname={MESSAGES_ALL_URL}>
					<MessageAll />
				</ProtectedRoute>
			}
		/>
		<Route
			path={MESSAGES_INDIVIDUAL_URL}
			element={
				<ProtectedRoute pathname={MESSAGES_INDIVIDUAL_URL}>
					<MessageIndividual />
				</ProtectedRoute>
			}
		/>
		<Route
			path={MESSAGE_SEND_TO_ALL_URL}
			element={
				<ProtectedRoute pathname={MESSAGE_SEND_TO_ALL_URL}>
					<SendMessageToAll />
				</ProtectedRoute>
			}
		/>
		<Route
			path={MESSAGE_SEND_TO_INDIVIDUAL_URL}
			element={
				<ProtectedRoute pathname={MESSAGE_SEND_TO_INDIVIDUAL_URL}>
					<SendMessageToIndividual />
				</ProtectedRoute>
			}
		/>
		<Route
			path={LEARNER_INFO_URL}
			element={
				<ProtectedRoute pathname={LEARNER_INFO_URL}>
					<LearnerInfo />
				</ProtectedRoute>
			}
		/>
		<Route path='*' element={<Navigate to={LOGIN_URL} />} />
	</Routes>
);

export default Router;
