import { PaperAirplaneIcon } from '@heroicons/react/24/outline';
import Button from '../../../components/Button/Button';
import { t } from 'i18next';
import { useNavigate } from 'react-router-dom';
import { MESSAGES_INDIVIDUAL_URL } from '../../../constants/router';

interface ActionButtonBoxInterface {
	activeEventFunction: () => void;
}

const ActionButtonBox = ({ activeEventFunction }: ActionButtonBoxInterface) => {
	const navigate = useNavigate();

	const executeActiveEvent = () => {
		activeEventFunction();
	};

	const executeCancelEvent = () => {
		navigate(MESSAGES_INDIVIDUAL_URL);
	};

	return (
		<div className='absolute top-[72px] right-[40px] flex gap-2 '>
			<Button className='h-fit' label={t('취소')} size='lg' color='gray' onClick={executeCancelEvent}></Button>
			<Button
				className='h-fit'
				leftIcon={<PaperAirplaneIcon />}
				label={t('보내기')}
				size='lg'
				color='black'
				onClick={executeActiveEvent}
			></Button>
		</div>
	);
};

export default ActionButtonBox;
